// Login Role Select / Index
.page-login {
  &__welcome--role-select {
    margin-top: 55px;
  }

  &__role-prompt {
    font-family: $avenir-hv;
    font-size: 18px;
    color: $blue-200;
    margin-bottom: 10px;
  }

  &__role-list {

    &-item {
      transition: all 350ms $easeOutQuad;
      border: 1px solid rgba(42, 56, 56, 0);
      border-bottom: 1px solid $blue-400;
      border-radius: 3px;

      &:hover {
        box-shadow: $btnHoverBoxShadow;
        border: 1px solid rgba(42, 56, 56, 0.05);

        a {
          font-family: $avenir-bl;
          color: $white;
          text-decoration: none;
          padding-left: 30px;
        }
      }

      &.no-border {
        border: 1px solid rgba(42, 56, 56, 0);
      }

      &.deselected a {
        color: $blue-200;
      }

      &:last-child {
        border-bottom: 0;
      }

      a {
        display: block;
        color: $white;
        font-family: $avenir-hv;
        font-size: 30px;
        transition: all 250ms $easeOutQuad;
        padding: 20px 20px 20px 0;

        &:focus {
          text-decoration: none;
        }
      }
    }
  }
}

@include mobile() {
  .page-login {
    // disabling hover state
    &__role-list-item:hover {
      box-shadow: none;
      border: 1px solid transparent;

      &:not(:last-child) {
        border-bottom: 1px solid #ABD5D5;
      }

      a {
        padding-left: 0;
      }
    }
  }
}
