@import 'bootstrap-variables';

//TODO: replace all instances of these colors with new color scheme vars ... resolve any that don't align.

// New Final (?) Color Scheme
$blue-50:  #EBEFF1; // or #ECEFF1 ?
$blue-100: #CFD8DC;
$blue-200: #B0BEC5;
$blue-300: #90A4AE;
$blue-400: #78909C;
$blue-500: #607D8B;
$blue-600: #546E7A;
$blue-700: #455A64;
$blue-800: #37474F;
$blue-900: #263238;

$orange:   #FF8A65;
$teal:     #99CCCC;
$white:    #FFF;
$black:    #000;

$grey-50:  #FAFAFA;
$grey-100: #F5F5F5;
$grey-200: #EEEEEE;
$grey-300: #E0E0E0;
$grey-400: #BDBDBD;
$grey-500: #9E9E9E;
$grey-600: #757575;
$grey-700: #616161;
$grey-800: #424242;
$grey-900: #212121;

$black-semi-transp: rgba(0, 0, 0, 0.08);
$black-less-transp: rgba(0, 0, 0, 0.16);

// for partially transparent linear gradients (ex. truncating list cells) in Safari
// https://stackoverflow.com/questions/30673204/css-linear-gradient-transparency-misbehaving-only-in-safari
$white-transp: rgba(255, 255, 255, 0);
$grey-50-transp: rgba(250, 250, 250, 0);
$grey-100-transp: rgba(245, 245, 245, 0);

$blue-50-transp: rgba(235, 239, 242, 0);


// var aliases
$warning: $orange;
$bodyBgColor: $grey-100;
$bannerBgColor: $orange;
$gridPad: $grid-gutter-width-base;


// misc values
$maxBodyContentWidth: 1680px;
$navBarExpandedWidth: 240px;  // TBD: may change to scale w body
$mobileMenuBodyShift: 20px;
$headerHeight: 64px;
$navMenuAnimSpeed: 300ms;


// shared styles
$btnHoverBoxShadow: 0 4px 7px 0 rgba(0, 0, 0, 0.15), 0 4px 8px 0 rgba(0, 0, 0, 0.12);
$moduleBoxShadow: 0 0.5px 1px 0 rgba(0,0,0,0.24), 0 0.5px 1.5px 0 rgba(0,0,0,0.12);
$popup-box-shadow: 0 1.5px 7px 0 rgba(0,0,0,0.15), 0 1.5px 7.5px 0 rgba(0,0,0,0.12);
$tableInnerShadow: 0 0 16px rgba(0,0,0,0.5);
$scrimColor: rgba(235, 239, 241, 0.5);
$popup-border-radius: 3px;
$btn-border-radius: 3px;
$moduleBorderRadius: 4px;


// z indices
$zindex-nav-bar: 20;
$zindex-mobile-popup-menus: 100;


// ascii codes
$asciiTriangleRight: '\25B6';
