.draggable-row {
  position: relative;
  display: flex;
  align-items: center;
  height: 60px;
  padding: 12px 8px 12px $gridPad/2;
  border-radius: 3px;
  border-bottom: 1px solid $black-semi-transp;
  transition: box-shadow 150ms $easeOutQuad;
  cursor: pointer;

  @include draggableGrippy(false);
  &:before,
  &:after {
    top: 8px;
  }

  &:hover {
    box-shadow: $moduleBoxShadow;
  }

  &:last-child {
    border-bottom: 0;
  }

  // shorten component name for inner els
  .dr {
    &__order-value {
      display: none;
      width: 28px;
      height: 28px;
      border: 2px solid $blue-200;
      border-radius: 50%;
      color: $blue-200;
      font-family: $avenir-bl;
      margin-right: 8px;
      padding-top: 3px;
      text-align: center;
    }

    &__delete-btn {
      display: none;
      position: absolute;
      right: $gridPad/4;
      top: 50%;
      transform: translate(0px, -50%);
      color: $blue-500;

      & > * {
        // actual "X" icon
        padding: 10px;
        font-size: 16px;
      }
    }
  }

  // mods to enable els
  &--deletable {
    &:hover {
      // move over delete btn when hovering
      // because the drag grippy gets shown
      .dr__delete-btn {
        transform: translate(-18px, -50%);
      }
    }

    .dr__delete-btn {
      display: block;
    }
  }

  &--ordered {
    .dr__order-value {
      display: block;
    }
  }
}
