$horz-pad: 24px;

.header {
  width: 100%;
  height: $headerHeight;
  background-color: $black;
  padding: 0 $gridPad;
  display: flex;
  align-items: center;
  font-family: $avenir-hv;
  font-size: 12px;
  color: $white;

  &__button {
    position: relative;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: $gridPad;

    &--has-new i {
      position: relative;

      &:after {
        @include orangeDot(8px);
        left: 4px;
        top: 4px;
      }
    }
  }

  &__burger {
    margin-left: 0;
  }

  &__content {
    flex-grow: 1;
    display: flex;
    align-items: center;
    // weird flexbox thing, this allows header__title to shrink all the way to 0
    // https://stackoverflow.com/questions/36247140/why-dont-flex-items-shrink-past-content-size#36247448
    min-width: 0;
    margin: 0 $gridPad;
  }

  &__title {
    margin-right: $horz-pad;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex-shrink: 1;
    font-size: 18px;
  }

  &__status {
    flex-shrink: 0;
    font-family: $avenir-md;
    line-height: 20px;
    border-radius: 10px;
    padding: 0 12px;
    background-color: $blue-500;

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__link {
    position: relative;
    line-height: $headerHeight;
    font-family: $avenir-hv;
    cursor: pointer;
    color: gray;

    &:not(:last-child) {
      margin-right: $gridPad;
    }

    &--active {
      color: $white;

      &:after {
        @include orangeUnderline();
      }
    }
  }

  &__right {
    display: flex;
    align-items: center;
    margin-left: auto;

    // ZD: temporary, should be based on longest text in list
    input.select-dropdown {
      width: 208px;
    }
  }
}
