//$horz-padding: 32px;
$vert-padding: 16px;
$line-height: 40px;
$font-size: 12px;
$input-font-size: 15px;
$table-row-height: 50px;

$head-height: 64px;
$head-bg: $blue-100;
$horz-padding: 24px;

.popup-menu {
  font-size: $font-size;
  font-family: $avenir-md;

  &__head {
    color: $white;
    background-color: $head-bg;
    line-height: $head-height;
    height: $head-height;
    padding: 0 $horz-padding;
    position: relative;
    box-shadow: $popup-box-shadow;
    border-top-left-radius: $popup-border-radius;
    border-top-right-radius: $popup-border-radius;
    overflow: hidden;

    span {
      white-space: nowrap;
    }

		&:before {
      font-family: 'icomoon';
      content: $icon-triangle;
      font-size: 24px;
      position: absolute;
      right: $horz-padding;
      top: 50%;
      transform: translateY(-50%);
      z-index: 1;
    }

    .popup-menu__head-shadow-hider {
      content: '';
      width: 100%;
      position: absolute;
      z-index: 1;
      bottom: 0;
      left: 0;
      height: 3px;
      background-color: $head-bg;
    }
  }

  &__body {
    position: absolute;
    width: 100%;
    left: 0;
    background-color: $white;
    box-shadow: $popup-box-shadow;
    border-bottom-left-radius: $popup-border-radius;
    border-bottom-right-radius: $popup-border-radius;
    //overflow: hidden;

    &--justify-left {
      border-top-right-radius: $popup-border-radius;
    }

    &--justify-right {
      left: auto;
      right: 0;
      border-top-left-radius: $popup-border-radius;
    }
  }

  &__options {
    padding: $vert-padding 0;
  }

  &__option {
    padding: 0 $horz-padding;
    line-height: $line-height;
    height: $line-height;
    position: relative;
    color: $blue-300;
    font-family: $avenir-md;
    cursor: pointer;

    &--selected {
      font-family: $avenir-hv;
      color: $blue-700;
    }

    span {
      white-space: nowrap;
      margin-right: 5px;
    }

		i {
      line-height: $line-height;
		}

    &:hover {
      background-color: $grey-50;
    }
  }

  &__inline-form {
    justify-content: space-between;
    align-items: flex-end;
    padding: $vert-padding $horz-padding $horz-padding;
    margin-bottom: 0;
    display: flex;

    .spacer {
      flex: 1 0 auto;
      height: 15px;
      position: relative;
      text-align: center;
      padding: 0 10px;

      &:after {
        content: '';
        display: inline-block;
        width: 9px;
        height: 1px;
      }

      &.minus:after {
        background-color: $blue-500;
      }
    }

    @include mdbSelectPopDown

    .select-wrapper {
      // temp fix until we have designs
      .caret {
        display: none;
      }

      input.select-dropdown {
        font-size: 15px;
        font-family: $avenir-md;
        margin: 0;
        // ZD: temp fix until we have designs
        height: 31px;
        color: $blue-500;
      }

      ul {
        margin-top: -8px;
      }

      li:first-child span {
        font-size: $input-font-size;
        font-family: $avenir-md;
      }
    }
  }

  &__inline-form-group {
    margin-bottom: 0 !important;

    label {
      font-size: 12px;
      line-height: $line-height;
      text-transform: uppercase;
      font-family: $avenir-hv;
      color: $blue-200 !important;
      margin-bottom: 0;
    }

    input {
      font-size: 15px;
      line-height: 30px;
      color: $blue-500;
      margin: 0;
      border-bottom: 1px solid $blue-500 !important;
      font-family: $avenir-md;
      height: auto;
      padding: 0;

      &:focus {
        color: $blue-500;
        box-shadow: 0 1px 0 0 $blue-500 !important;
      }
    }
  }

  &__confirm-buttons {
    display: flex;
    background-color: $blue-100;
    border-bottom-left-radius: $popup-border-radius;
    border-bottom-right-radius: $popup-border-radius;
    overflow: hidden;

    div {
      font-family: $avenir-hv;
      color: $white;
      padding: 12px $horz-padding;
      text-transform: uppercase;
      width: 50%;
      text-align: center;
      line-height: $line-height;
      cursor: pointer;

      &:not(:last-child) {
        @include cellBorders($right: $blue-500);
      }

      &:hover {
        color: $blue-500;
      }
    }
  }
}
