.comment {
  &-icon {
    flex: 0 0 auto;
    width: $gridPad;
    height: $gridPad;
    color: white;
    font-size: 10px;
    font-family: $avenir-hv;
    background-color: $blue-200;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-header {
    line-height: $gridPad;
    margin-bottom: 10px;
  }

  &-text {
    font-size: 12px;
    padding-left: $gridPad/2;
    font-family: $avenir-bk
  }

  &-name {
    font-family: $avenir-md;
    color: $grey-800
  }

  &-organization {
    font-family: $avenir-bk-it;
    color: $grey-600;
    margin-right: $gridPad/2;
  }

  &-time {
    color: $grey-500;
  }

  &-message {
    color: $grey-700;
  }
}