@import '../mixins';
@import '../variables';

@include mobile() {
  .page-dashboard {

    h1 {
      font-size: 27px;
      margin-bottom: 18px;
    }

    .row--mobile-no-gutters {
      h1 {
        padding-left: $gridPad;
      }
    }

    .dashboard__module {
      // undo styles for overflowing is-new dots
      .dashboard-list,
      .list-item {
        width: 100%;
        left: 0;
      }

      .list-item {
        padding-left: 57px;

        // tweak orange dot
        &--is-new:before {
          left: $gridPad;
          transform: translateY(-50%);
          width: 10px;
          height: 10px;
        }
      }

      .list__header,
      .list__container {
        border-radius: 0;
      }

      &.calendar {
        .calendar-select {
          &.is-desktop {
            display: none;
          }

          &.is-mobile {
            display: inline-block;
            margin-bottom: $gridPad;
            margin-left: $gridPad;

            select {
              border-width: 2px;
              font-size: 27px;
              height: 40px;
            }

            &.month {
              width: 170px;
            }

            &.year {
              width: 100px;
            }
          }
        }

        .calendar-container {
          border-radius: 0;
          flex-direction: column;
          height: 564px;
        }

        .dashboard-list {
          height: 345px;
        }

        .no-events {
          height: auto;
          flex: 1 0 auto;
        }

        .imported-week {
          display: inline-block;
        }

        .clndr-grid {
          width: 100%;
          z-index: 1;
          border-bottom: 1px solid $black-semi-transp;

          .days {
            white-space: nowrap;
            transform: translateX(0%);
            transition: all 0.5s;

            .imported-week {
              display: inline-block;
            }
          }

          .mobile-hidden-week {
            display: none;
          }

          .header-day,
          .day {
            height: 70px;
            font-size: 12px;
          }

          .day {
            // no styling for adjacent-month days
            &.adjacent-month {
              color: $blue-500;
              &.event:not(.selected) .day-text:after {
                background-color: $blue-500;
              }
            }

            &.selected:after {
              content: '';
              position: absolute;
              bottom: 0;
              left: 50%;
              width: 14px;
              height: 14px;
              background: $white;
              transform: translate(-50%, 50%) rotate(45deg);
              border-top: 1px solid $black-semi-transp;
              border-left: 1px solid $black-semi-transp;
            }

            &.event:not(.selected) .day-text:after {
              top: 18px;
            }

            .day-square {
              width: 40px;
              height: 40px;
              align-self: flex-start;
            }
          }

          .week {
            display: inline-block;
            width: 100%;
          }
        }
      }

      &.notifications,
      &.interviews {
        .slick-list {
          // has overflow hidden which prevents
          // new orange dot (on top) and box
          // shadow (on bottom) from showing
          padding-top: 10px !important;
          padding-bottom: 2px !important;
        }

        .dashboard-list {
          overflow: hidden;
          width: 100%;
          left: auto;
        }

        .list__container {
          box-shadow: none;
        }

        .list__header {
          display: none;
        }

        .list__count {
          padding: 12px 0 0 $gridPad;
          font-size: 12px;
          color: $blue-200;
        }

        .list-item {
          width: 100%;
          height: auto;
          left: auto;
          padding: 28px 76px 28px 32px;
          margin: 0 $gridPad/4;
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
          flex-direction: column;
          align-items: flex-start;
          border-bottom: 0;
          background-color: $white;
          border-radius: 3px;

          &--is-new:before {
            width: 20px;
            height: 20px;
            top: 0;
            left: -10px;
          }

          &__name-desc {
            flex: 1 0 0;
            order: 2;
          }

          &__time {
          }

          & > div {
            flex: 1 0 0 !important;
          }
        }
      }
    }
  }
}
