@import '../easing';
@import '../mixins';
@import '../variables';

$navBarHeight: 115px;

@include mobile() {
  .nav-bar {
    position: relative;
    width: 100%;
    height: $navBarHeight;
    padding: $gridPad/2;
    overflow: visible;

    &__logo {
      text-align: left;
      margin: 0;
    }

    &__burger {
      top: 4px;
      right: 4px;
      padding: 34px;
    }

    &__menu-scrim {
      display: none;
    }

    &__items {
      display: none;
      margin: 0 auto;
      white-space: nowrap;
      position: absolute;
      z-index: 2;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 0 $gridPad/2;
      background-color: $blue-400;

      // mobile takes the desktop nav menu and makes some
      // into this bar - To Do, Messages, Bookmarks - and
      // others into a full screen menu that slides in from the left.
      &--mobile-bar {
        display: flex;
        flex: 1 0 0;
        justify-content: space-between;

        .nav-bar__item {
          position: relative;
          display: inline-block;
          margin: 0;

          &.has-caret:after,
          &.has-caret.show-caret:after {
            transform: none;
          }

          &.has-caret:after {
            // convert caret notch into underlining white bar
            top: auto;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 2px;
          }

          &--has-new:before {
            left: 5px;
            top: 50%;
          }

          &-count {
            padding: 0;
          }

          a {
            font-size: 15px;
            padding: 10px 15px;
          }
        }
      }

      &--mobile-menu {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0 !important;
        padding: 0;
        background-color: $blue-500;

        overflow: auto;
        -webkit-overflow-scrolling: touch;

        visibility: hidden;
        transform: translateX(-100%);
        transition: transform $navMenuAnimSpeed $easeOutQuad, visibility 0ms linear $navMenuAnimSpeed;

        &.active {
          transform: translateX(-$mobileMenuBodyShift);
          visibility: visible;
          transition: transform $navMenuAnimSpeed $easeOutQuad, visibility 0ms linear 0ms;
        }

        &.active--sub-menu {
          transform: translateX(-100%);
        }

        .nav-bar__item {
          margin: 0;
          border-bottom: 1px solid $blue-600;

          a {
            color: $blue-50;
            padding: $gridPad;
          }

          i {
            float: right;
            color: #8FA4AE;
            font-size: 1.8em;
            line-height: 0.7em;
          }

          &--mobile-search {
            font-size: 15px;
            background-color: $blue-400;

            a {
              line-height: 1.4em;
              padding: 25px $gridPad 26px;
            }

            i {
              font-size: 1.4em;
              line-height: 1em;
              color: $blue-50;
            }

            i.search-icon {
              float: left;
              margin-right: 5px;
            }
          }
        }
      }
    }
  }


  /////////////////////////////////////////////////////////////////////////
  /////////////////////////////////////////////////////////////////////////
  /////////////////////////////////////////////////////////////////////////


  .nav-bar-menu {
    position: fixed;
    width: 100%;
    min-width: auto;
    height: calc(100% - 115px); // $navBarHeight
    top: $navBarHeight;
    transform: translateY(-100%);
    visibility: hidden;

    // transition: visibility 0, transform 300ms $easeOutQuad;

    &.active {
      visibility: visible;
      transform: translateY(0%);
      box-shadow: none;
    }

    &-scrim {
      display: none;
    }

    &__header {
      .close-btn {
        i.icon-back {
          display: none;
        }

        i.icon-x {
          display: block;
        }
      }
    }

    // change from the dropdown style of ToDo, Messages, and Bookmarks
    &--mobile-submenu {
      top: 0;
      height: 100%;
      z-index: $zindex-nav-bar + 1;
      transform: translateX(100%);

      &.active {
        transform: translateX(-$mobileMenuBodyShift);
      }

      .nav-bar-menu__header {
        .close-btn {
          i.icon-back {
            display: block;
          }

          i.icon-x {
            display: none;
          }
        }
      }
    }

    .help-content-guide {
      margin-top: 0;
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
