@import 'comments';
@import 'draggable-row';
@import 'simple-row';
@import 'simple-table';
@import 'approver';

.modal {
  // bootstrap provides the basics: fixed, 100%, z-index, etc
  h6 {
    font-family: $avenir-hv;
    font-size: 15px;
    font-weight: normal;
    color: $blue-200;
    margin-bottom: 22px;

    &:not(:first-child) {
      margin-top: 8px;
    }
  }

  .btn {
    margin: 0;
    box-shadow: $moduleBoxShadow;
  }

  // bootstrap overrides
  &-dialog {
    // the top position for the entire modal
    margin-top: 175px;
  }

  &-content {
    border: 0;
    border-radius: 3px;
    background-color: transparent;
  }

  &-header {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 26px;
    font-family: $avenir-hv;
    font-size: 24px;
    line-height: 27px;
    color: $blue-200;
    border-bottom: none;

    .modal-alt-title {
      font-family: $avenir-md;
    }

    &__close {
      color: $blue-500;
      cursor: pointer;
      // align w right edge of modal
      margin-right: -6px;

      i {
        font-size: 28px;
      }
    }
  }

  &-warning {
    background-color: $warning;
    width: 100%;
    padding: $gridPad/2 $gridPad;
    color: $white;
    font-family: $avenir-hv;

    &__icon {
      vertical-align: middle;
      font-size: 18px;
      margin-right: $gridPad/2;
    }
  }

  &-panel {
    box-shadow: 0 0.5px 1px 0 rgba(0,0,0,0.24), 0 0.5px 1.5px 0 rgba(0,0,0,0.12);
  }

  &-body {
    background-color: $white;
    padding: 0;
    font-size: 12px;
    font-family: $avenir-md;
    font-color: $grey-800;

    a,
    a:hover {
      color: $blue-500;
      text-decoration: underline;
    }

    .btn {
      margin: $gridPad/4 0;
    }

    // verify for consistency
    textarea.md-textarea {
      font-size: 12px;
    }
  }

  &-nav {
    padding-left: $gridPad/2;

    &__tab {
      color: $blue-200;
      font-size: 15px;
      font-family: $avenir-hv;
      display: inline-block;
      position: relative;
      height: 37px;
      margin: 0 $gridPad/2;
      cursor: pointer;

      &--active {
        color: $blue-700;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          top: 100%;
          width: 100%;
          height: 2px;
          z-index: 999999;
          background-color: $blue-700;
        }
      }
    }
  }

  //ex.: https://cl.ly/0c172c0E1d0A
  &-ordered-nav {
    padding: 0;
    margin: 0;
    display: flex;

    &__item {
      position: relative;
      flex: 1 0 33%;
      text-align: center;
      font-family: $avenir-hv;
      color: $grey-400;
      padding: $gridPad/2 0;
      list-style-position: inside;
      background-color: $grey-50;
      border-bottom: 1px solid $grey-200;

      $navH: 58px;
      $triSize: cos(45deg) * $navH;
      @mixin triangleEdge() {
        content: '';
        position: absolute;
        z-index: 1;
        display: block;
        width: $triSize;
        height: $triSize;
        top: ($navH - $triSize) / 2;
        right: -$triSize/2;
        transform: rotate(-45deg);
      }

      // light grey stroked right edge triangle
      &:after {
        @include triangleEdge;
        border-right: 1px solid $grey-200;
        border-bottom: 1px solid $grey-200;
      }

      &--active {
        color: $blue-700;
        border-bottom: 2px solid $blue-700;
        background-color: $blue-50;

        // when active and not in the first position,
        // needs a light grey cutout on the left edge
        &:not(:first-child):before {
          @include triangleEdge;
          right: auto;
          left: -$triSize/2;
          background-color: $grey-50;
          border-right: 1px solid $grey-200;
          border-bottom: 1px solid $grey-200;
        }

        // right-pointing blue triangle edge
        &:after {
          @include triangleEdge;
          border: 0;
          background-color: $blue-50;
        }
      }

      // no triangle edge on last item, active or not
      &:last-child:after {
        border: 0;
        background-color: transparent;
      }
    }
  }

  &-section {
    background-color: #fff;
    padding: $gridPad;
    position: relative;
    flex: 1 1 auto;

    &--col-pad {
      padding: $gridPad $gridPad/2;
    }

    &--static-header {
      background-color: $grey-50;
      color: $blue-700;
      font-family: $avenir-hv;
      font-size: 15px;
      line-height: 18px;
      padding: 20px $gridPad 19px;
    }

    &--footer-like {
      padding: 12px $gridPad;
      background-color: $grey-50;

      .form-inline {
        margin: 0;
      }
    }

    &:not(:last-child) {
      border-bottom: 1px solid $black-semi-transp;
    }
  }

  &-comments-list {
    height: 471px;
    border-bottom: 1px solid $black-semi-transp;
    position: relative;

    .modal-comment {
      padding: $gridPad $gridPad 27px $gridPad;
      display: flex;

      // last comment still has ps__rail-x and ps__rail-y after
      &:not(:nth-last-child(3)) {
        border-bottom: 1px solid $black-semi-transp;
      }
    }
  }

  // ex.: https://cl.ly/3f0a0E3c3g2O
  &-sidebar {
    background-color: $grey-50;
    border-radius: 3px;
    padding: $gridPad/2;
    margin-bottom: $gridPad/2;

    .ms__sum-table {
      &-item {
        display: flex;
        justify-content: space-between;
        border-top: 1px solid $black-semi-transp;
        color: $grey-600;
        padding: $gridPad/2 0;

        &:first-child {
          padding-top: 0;
          border-top: 0;
        }

        &-value {
          flex: 0 0 auto;
        }

        &--total {
          font-family: $avenir-hv;
          color: $grey-800;
          border-color: $grey-800;
          padding-bottom: 0;
        }
      }
    }
  }

	&-log-item {
    margin-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 20px;

		display: flex;
    justify-content: space-between;

		&:not(:last-child) {
			border-bottom: 1px solid $black-semi-transp;
		}

    .log-item-title {
      color: $grey-800;
      font-family: $avenir-hv;
    }

    .log-item-info {
      color: $grey-600;
      font-family: $avenir-bk;
    }
	}

  &-text-block {
    margin-bottom: 24px;
  }

  &-table {
    font-size: 12px;
    width: 100%;
    color: $grey-800;
    margin-bottom: $gridPad/2;

    thead th {
      text-transform: uppercase;
      font-family: $avenir-hv;
      @include cellBorders($bottom: $black-less-transp);
      padding-bottom: $gridPad/2;
    }

    tbody {
      td {
        vertical-align: top;
        padding: $gridPad/2 0;
      }

      td {
        &:not(:first-child) {
          color: $grey-600;
        }

        &:first-child {
          padding-right: 32px;
          white-space: nowrap;
        }
      }

      tr:not(:last-child) td {
        @include cellBorders($bottom: $black-semi-transp);
      }
    }
  }

  &-rate-proposal {
    display: flex;

    &:not(:last-child) {
      margin-bottom: $gridPad;
    }

    .proposal-user {
      text-transform: capitalize;
      line-height: 0.5;
      color: $grey-800;
    }

    .proposal-spacer {
      width: 32px;
    }

    .proposal-bubble {
      position: relative;
      display: flex;
      background-color: lightgray;
      padding: 24px;
      border-radius: 3px;
      border-top-left-radius: 0;

      // caret
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: -$gridPad/2;
        border-bottom: 16px solid transparent;
        border-right:16px solid lightgray;
      }
    }

    .proposal-label {
      color: $white;
      line-height: 1;
      background-color: $grey-800;
      padding: 6px $gridPad/2;
      border-radius: $gridPad/2;
      margin: 0 6px;
    }

    .proposal-label-container {
      position: absolute;
      top: -7px;
      left: -$gridPad/2 - 6px;
      display: flex;
    }

    &--user-vendor {
      .proposal-label-container {
        left: auto;
        right: -$gridPad/2 - 6px;
      }
    }

    &--user-client {
      flex-direction: row-reverse;

      .proposal-bubble {
        background-color: gray;
        border-top-left-radius: 3px;
        border-top-right-radius: 0;

        // caret
        &:before {
          left: auto;
          right: -$gridPad/2;
          border-right: 0;
          border-bottom: 16px solid transparent;
          border-left:16px solid gray;
        }
      }
    }

    .proposal-col-sm {
      font-family: $avenir-hv;
      font-size: 15px;
    }

    .proposal-col-lg {
    }

    .proposal-comment {
      margin-bottom: 6px;
    }


  }

  &-footer {
    padding: $gridPad/2;
    background-color: $grey-50;
    border-top: 1px solid rgba(0,0,0,0.08);

    &--multi-cta {
      justify-content: space-between;
    }

    .modal-footer-form {
      flex-grow: 1;
      margin: 0 32px 0 0;
    }

    .modal-footer-info {
      font-size: 12px;
      font-family: $avenir-md;
      font-color: $grey-800;
      flex: 1 0 auto;
    }

    .btn + .btn {
      margin-bottom: 0;
      margin-left: $gridPad/4;
    }
  }

  &-inline-atc {
    display: inline-block;

    .atc-style-menu-wb {
      margin-left: 12px;
      vertical-align: middle;
    }

    .atcb-list {
      top: calc(100% + 17px);
      left: 100%;
      transform: translateX(-100%);
    }
    .atcb-link {
      margin: 0 21px;
    }
  }

  &-backdrop {
    background-color: $grey-100;
  }
}

// more BS overrides -- will prob need to be adjusted
@media (min-width: 576px) {
  .modal-dialog {
    max-width: 824px;
  }
}

@media (min-width: 992px) {
  .modal-lg {
    max-width: 1022px;
  }
}
