.page-login {
  &__terms-box {
    border: 1px solid #E3F1F1;
    border-radius: 3px;
    height: 100px;
    overflow: auto;
    padding: 20px 20px 0 20px;
    font-family: $avenir-bk;

    h4 {
      font-family: $avenir-hv;
      color: $blue-50;
    }

    p {
      font-size: 15px;
      color: $blue-200;
    }
  }

  &__checkbox {
    font-size: 12px;
    font-family: $avenir-md;
  }

  & [type="checkbox"] + label:before {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    width: 16px;
    height: 16px;
    z-index: 0;
    border: 1px solid #E3F1F1;
    border-radius: 3px;
    margin-top: 0px;
    transform: none;
    box-shadow: none !important;
  }

  & [type="checkbox"]:checked + label:after {
    content: '';
    position: absolute;
    top: 8px;
    left: 3px;
    width: 10px;
    height: 5px;
    border-left: 2px solid #E3F1F1;
    border-bottom: 2px solid #E3F1F1;
    transform: rotate(-45deg);
  }
}
