@import '../variables';

$bannerVertPad: 11px;
$bannerHorzPad: 32px;

@include desktopExpandedNav() {
  body.nav-bar-expanded {
    .banner {
      margin-left: $navBarExpandedWidth;
    }
  }
}

.banner {
  position: relative;
  background-color: $bannerBgColor;
  color: $white;
  font-size: 12px;
  padding: 0 $bannerHorzPad;
  transition: height 400ms $easeInOutQuad;

  &__header {
    height: 40px;
    display: flex;
    align-items: center;
    position: relative;
  }

  &--state-expand {
    .icon-expand {
      opacity: 0;
    }

    .icon-collapse {
      opacity: 1;
    }

    .banner__content {
      visibility: visible;
      opacity: 1;
    }
  }

  &--state-collapse {
    height: 40px;

    .icon-expand {
      opacity: 1;
    }

    .icon-collapse {
      opacity: 0;
    }

    .banner__content {
      visibility: hidden;
      opacity: 0;
      transition: opacity 200ms $easeOutQuad, visibility 0ms linear 200ms;
    }
  }

  &__alert-icon {
    margin-right: 1em;
  }

  &__expand-icon {
    position: absolute;
    right: 0;
    width: 24px;
    height: 100%;
    cursor: pointer;
    display: flex;

    i {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0 -50%);
      transition: opacity 150ms $easeOutQuad;
      transform: translate(0 -50%);
    }
  }

  &__content {
    margin: 0 0 $bannerVertPad 0;
    transition: opacity 200ms $easeOutQuad, visibility 0ms linear;
  }

  // banner types
  &.banner-system-maintenance {
    .banner__icon {
      color: $white;

      i {
        font-size: 14px;
      }
    }
  }
}
