.category-table {
  border: 1px solid $black-semi-transp;
  border-radius: 3px;
  display: flex;

  // TODO: should this scroll or just grow?
  // if scroll we should use PerfectScrollbar
  height: 192px;
  overflow-y: scroll;

  &__col {
    flex: 1 0 auto;
    max-width: 50%;

    &:first-child {
      border-right: 1px solid $black-semi-transp;
    }
  }

  &__col-item {
    color: $blue-500;
    padding: 7px $gridPad/2;
    cursor: pointer;
    position: relative;

    &:hover,
    &.active {
      color: $white;
      background-color: $blue-600;
    }

    &--cat:hover,
    &--cat.active {
      &:after {
        color: $white;
      }
    }

    &--cat:after {
      color: $blue-500;
      content: $asciiTriangleRight;
      position: absolute;
      right: $gridPad/2;
    }
  }
}
