@import 'bootstrap/scss/variables';
@import 'bootstrap/scss/mixins';
@import 'variables';
@import 'icons';

@mixin mobile() {
  @include media-breakpoint-down(sm) {
    @content;
  }
}

// used rarely since we're designing desktop-first
@mixin desktop() {
  @include media-breakpoint-up(md) {
    @content;
  }
}

// larger than `desktop()` above
@mixin desktopExpandedNav() {
  @media (min-width: 992px) {
    @content;
  }
}

// should be placed in a :before or :after pseudo el
@mixin orangeDot($size) {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%);
  width: $size;
  height: $size;
  border-radius: 50%;
  background-color: $orange;
}

// should be placed in a :before or :after pseudo el
@mixin orangeUnderline() {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: $orange;
}

@mixin rangeSlider() {
	// gotta hide this - from MDBootstrap
	.thumb {
		display: none;
	}

	.rangeslider {
		background-color: $blue-100;
		height: 2px;
		position: relative;

		&__fill {
			background-color: $blue-500;
			height: 100%;
		}

		&__handle {
			position: absolute;
			top: -5px;
			width: 12px;
			height: 12px;
			border-radius: 50%;
			background-color: $blue-500;
		}
	}
}

@mixin mdbSelectPopDown() {
  $header-color: $white;
  $option-color: $black;
  $rule-color: $orange;
  $header-bg: $blue-200;
  $font-size: 12px;
  $horz-padding: 24px;
  $vert-padding: 12px;
  $line-height: 40px;
  $select-height: 64px;

  .select-wrapper {
    margin: 0 $horz-padding;

    // horz line
    &:after {
      @include orangeUnderline;
    }

    // caret
    span.caret {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      z-index: 1000;
      font-size: 0; // hide MDBootstrap default ▼ char
      border-left: 7px solid $header-color;
      border-bottom: 7px solid transparent;
      transform: translate(0px, -7px) scaleX(0.8) rotate(-135deg);
    }

    input.select-dropdown {
      font-size: $font-size;
      font-family: $avenir-hv;
      color: $header-color;
      height: $select-height;
      margin: 0;
      border: 0;
    }

    ul.dropdown-content {
      width: calc(100% + (2 * #{$horz-padding})) !important;
      left: 50% !important;
      transform: translateX(-50%);
      border-bottom-left-radius: 3px;
      border-bottom-right-radius: 3px;
      padding-bottom: $vert-padding;
      overflow: hidden;
    }

    li {
      position: relative;
      line-height: normal;
      padding-left: 0 !important;

      span {
        font-size: $font-size;
        font-family: $avenir-md;
        color: $option-color;
        padding: 0 $horz-padding;
        line-height: $line-height;
        white-space: nowrap;
        position: relative;
      }

      &.active {
        background-color: inherit;

        span:after {
          content: '';
          width: 2px;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background-color: $rule-color;
        }
      }

      &.new-group:before {
        content: '';
        display: block;
        width: 100%;
        height: $vert-padding * 2 + 1;
        background: $header-bg;
        border-top: $vert-padding solid $white;
        border-bottom: $vert-padding solid $white;
      }

      &.leading-icon-bookmark {
        span:before {
          font-family: 'icomoon';
          content: $icon-bookmark-fill;
          margin-right: 18px;
          font-size: 18px;
          vertical-align: bottom;
        }
      }

      // open menu header
      &:first-child {
        background: $header-bg !important;
        margin-bottom: $vert-padding;

        & > span {
          position: relative;
          font-family: $avenir-hv;
          position: relative;
          line-height: $select-height;
          color: $header-color;
        }
      }
    }
  }

  // Will be default browser dropdown, so we just style the select to match the desktop input el
  .is-mobile {
    position: relative;

    span.caret {
      position: absolute;
      right: 0;
      top: 50%;
      font-size: 0;
      border-left: 7px solid $header-color;
      border-bottom: 7px solid transparent;
      transform: translate(0px, -7px) scaleX(0.8) rotate(-135deg);
    }

    select {
      height: $select-height;
      display: block;
      width: 100%;
      color: $option-color;
      background: none;
      border: none;
      border-bottom: 2px solid $rule-color !important;
      border-radius: 0;
      font-size: $font-size;
      font-family: $avenir-hv;
      appearance: none;
      -webkit-appearance: none;
      -moz-appearance: none;
    }

    select:focus {
      outline: none;
    }

    select::-ms-expand {
      display: none;
    }
  }
}

// should be placed in :before or :after pseudo-el
@mixin cellTruncateGradient($color, $width, $height:40px) {
  // Safari quirk: Fading from full-transparent to a color requires that the transparency has the correct chroma
  // https://stackoverflow.com/questions/30673204/css-linear-gradient-transparency-misbehaving-only-in-safari
  $color-transp: rgba($color, 0);

  width: $width;
  background: linear-gradient(to right, $color-transp, $color 50%);
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  box-shadow: inherit;
  // assume 50px unless specified
  height: $height;
  // clip out the left and bottom shadow in case inherited from parent
  clip: rect(0px, $width, $height - 1, 1px);
}


@mixin cellBorders($top:null, $right:null, $bottom:null, $left:null, $important:null) {
  $box-shadow: null;

  @if $top {
    $box-shadow: append($box-shadow, inset 0 1px #{$top}, comma);
  }
  @if $right {
    $box-shadow: append($box-shadow, inset -1px 0 #{$right}, comma);
  }
  @if $bottom {
    $box-shadow: append($box-shadow, inset 0 -1px #{$bottom}, comma);
  }
  @if $left {
    $box-shadow: append($box-shadow, inset 1px 0 #{$left}, comma);
  }
  @if $important {
    $box-shadow: append($box-shadow, !important, space);
  }

  box-shadow: $box-shadow;
}

// used in table drop zone to make a lot of adjacent cells look like a single div with inner shadow
@mixin cellInsetShadowLeftRight() {
  box-shadow: -1px 0 $grey-300, 1px 0 $grey-300, inset -1px 0 rgba(0,0,0,0.1), inset -2px 0 rgba(0,0,0,0.1), inset -3px 0 rgba(0,0,0,0.1), inset -4px 0 rgba(0,0,0,0.1), inset -5px 0 rgba(0,0,0,0.1), inset 1px 0 rgba(0,0,0,0.1), inset 2px 0 rgba(0,0,0,0.1), inset 3px 0 rgba(0,0,0,0.1), inset 4px 0 rgba(0,0,0,0.1), inset 5px 0 rgba(0,0,0,0.1);
}

// originally this comes from AccelTables in which the <th> column
// headers use box-sizing: content-box. To use this mixin elsewhere
// pass false
@mixin draggableGrippy($useContentBox: false, $height: 28px, $color: $blue-50) {
  &:after,
  &:before {
    @if $useContentBox == true {
      width: 2px;
    } @else {
      width: 6px;
    }
    content: '';
    position: absolute;
    border-left: 2px solid $color;
    border-right: 2px solid $color;
    height: $height;
    top: 4px;
    right: 10px;
    display: none;
  }

  &:before {
    right: 18px;
  }

  &:hover:after,
  &:hover:before {
    display: block;
  }
}


@mixin absCenter() {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}


// define animation keyframes within target declaration
@mixin animation-keyframes {
  $animation-name: unique-id();
  animation-name: $animation-name;

  @keyframes #{$animation-name} {
    @content;
  }
}
