@import '../variables';
@import '../easing';
@import '../fonts';
@import '../mixins';

.page-login {
  text-align: left;
  background-color: $blue-500; // this is changing to $blue-500 probably but other colors need to be tweaked first
  color: $white;

  a:hover {
    color: $white;
    transition: all 200ms $easeOutQuad;
  }

  .main-container {
    height: 100%;
    padding: $grid-gutter-width-base;
    max-width: 100%;
  }

  &__logo {
    float: left;
    max-width: 176px;

    img {
      width: 100%;
    }
  }

  &__body {
    // TODO: would prefer to do this with flexbox .. figure that out
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 1008px;

    &.is-relative {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      margin: 60px auto 100px;
    }
  }

  &__welcome {
    margin-top: 15px;

    &-body {
      margin-top: 25px;
      color: $blue-200;
      font-size: 18px;

      a {
        color: $blue-50;
        font-family: $avenir-hv;

        &:hover {
          color: $white;
          text-decoration: none;
        }
      }

      &--note {
        font-family: $avenir-hv;
        color: #F5FAFA;
      }

      // back arrow icon
      i {
        font-size: 1.1em;
        line-height: 1.4em;
        vertical-align: top;
      }
    }
  }

  &__messaging {
    font-size: 18px;
    color: $blue-200;
    font-family: $avenir-md;
  }

  &__login-form {
    button {
      margin: 0;
      color: $blue-500 !important;
      background-color: $white;

      &.btn-primary {
        &:hover,
        &:focus {
          background-color: $blue-50 !important;
          box-shadow: none !important;
        }
      }
    }

    label {
      & {
        font-size: 1em;
        color: $blue-50;
        font-family: $avenir-hv;
        text-transform: none;
        top: 0.8rem;
      }
      &.active {
        font-size: 1em;
        color: $blue-200;
        font-family: $avenir-hv;
      }
    }


    // TODO: may want to move much of this to a global style for other
    // sections where form fields will also appear, even if styled slightly
    // differently, diff't colors, etc

    input[type=checkbox] {
      // enables checkbox to receive focus
      // even though it's hidden by in MDBootstrap by left: -999999px
      &:checked,
      &:not(:checked) {
        visibility: visible;
      }

      &:focus ~ label:before {
        // this is the checkbox that's rendered
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
      }
    }

    input[type=text],
    input[type=password],
    input[type=email] {
      color: $blue-50;
      font-size: 21px;
      font-family: $avenir-hv;
      box-shadow: 0 1px $blue-50;
      border-bottom: 0;

      &:focus {
        border-bottom: 0;
        box-shadow: 0 2px $white;
        color: $white;

        & + label {
          font-size: 1em;
          color: $blue-200;
        }
      }

      & > span {
        transition: opacity 200ms $easeOutQuad;
      }

      &.invalid {
        box-shadow: 0 1px $warning;
      }

      &:focus.invalid {
        box-shadow: 0 2px $warning;
      }

      &.invalid + label,
      &:focus.invalid + label {
        & > span {
          opacity: 0;
        }
      }

      &.invalid + label:after,
      &:focus.invalid + label:after {
        // color will likely change
        color: $warning;
      }

      &.valid + label:after,
      &:focus.valid + label:after {
        color: $blue-50;
      }

      &.invalid + label:after,
      &:focus.invalid + label:after,
      &.valid + label:after,
      &:focus.valid + label:after {
        font-family: $avenir-hv;
        top: 0;
        // so that the error msg can go beyond
        // width of the input's label.
        // however, be careful not to provide
        // messages that are too long.
        white-space: nowrap;
      }

      // chrome specific - when someone uses a saved value
      // get rid of the default yellow background.
      &:-webkit-autofill,
      &:-webkit-autofill:hover,
      &:-webkit-autofill:focus,
      &:-webkit-autofill:active {
        -webkit-box-shadow: 0 0 0px 1000px $teal inset;
        -webkit-text-fill-color: $white !important;
      }
    }

    input[type=password] {
      letter-spacing: 6px;
    }

    .md-form {
      margin-bottom: 65px;
    }
  }

  &__footer {
    position: relative;
    margin-bottom: $grid-gutter-width-base;

    &.is-fixed {
      position: absolute;
      left: $grid-gutter-width-base;
      right: $grid-gutter-width-base;
      bottom: $grid-gutter-width-base;
      margin-bottom: 0;
    }
  }

  &__help-prompt {
    float: left;

    a {
      color: $blue-50;
      font-family: $avenir-hv;
      font-size: 16px;

      &:hover {
        color: $white;
        text-decoration: none;
      }
    }
  }

  &__desktop-help-panel {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    bottom: 0;
    // okay? doesn't really make sense to use grid here
    width: 240px;
    height: 100%;
    background: $white;
    transform: translateX(-100%);
    transition: all 300ms $easeOutQuad;

    &.active {
      transform: translateX(0%);
      box-shadow: 2px 0 20px rgba(0, 0, 0, 0.2);  // unsure exact spec
    }

    &-header {
      cursor: pointer;
      color: $blue-200;
      background-color: $grey-50;
      border-bottom: 1px solid $black-semi-transp;
      padding: 30px 32px;

      i {
        color: $blue-500;
        float: right;
      }
    }

    .help-content-guide {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;

      a:hover {
        text-decoration: underline;
      }
    }
  }

  &__desktop-help-scrim {
    position: fixed;
    z-index: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0);

    display: none;
    &.active {
      display: block;
    }
  }

  &__mobile-help-content {
    display: none;
  }

  &__copyright {
    float: right;
    color: $blue-200;
    font-size: 12px;
    padding-top: 5px; // kinda dumb - would rather force baseline alignment w the larger help div
  }

  @import '../common/help_content';
}


@import 'role_select';
@import 'client_login';
@import 'first_time';
@import 'forgot_username_password';
@import 'lang_select';

@import 'mobile';
