//
// TODO: make sure these imports here and in other files aren't making redundant styles
//
@import 'easing';
@import 'variables';
@import 'fonts';
@import 'icons';
@import 'mixins';
@import 'trigonometry';
@import 'common/modals';
@import 'common/typeahead';
@import 'common/rating-stars';
@import 'common/evaluation';
@import 'common/header';
@import 'utils/dev_grid';

body {
  background: $bodyBgColor;
  font-family: $avenir-bk;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &.no-scroll {
    height: 100%;
    overflow: hidden !important;
  }
}

a {
  color: $white;
  transition: color 200ms $easeOut;

  &:hover,
  &:focus {
    color: inherit;
    text-decoration: underline;
  }
}

h1 {
  font-family: $avenir-bl;
  font-size: 45px;
}

h2 {
  font-family: $avenir-hv;
  font-size: 30px;
  line-height: 36px;
}

h3 {
  font-family: $avenir-hv;
  font-size: 21px;
}

h4 {
  font-family: $avenir-hv;
  font-size: 15px;
}

img {
  // odd bootstrap style
  border-radius: 0;
}

.main-container {
}

.nav-bar-container {
  // wrapper where the bs column class is set to get flush bg color
  position: fixed;
  z-index: $zindex-nav-bar;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
}

.body-container {
  width: 100%;
  max-width: $maxBodyContentWidth;
  padding: $gridPad $gridPad 0;
}

@include desktopExpandedNav() {
  body.nav-bar-expanded {
    .nav-bar-container {
      width: $navBarExpandedWidth;        // TBD: this could change
    }

    .header,
    .body-container {
      width: calc(100% - #{$navBarExpandedWidth});
      margin-left: $navBarExpandedWidth;  // TBD: if nav bar width changes, this goes away
    }
  }
}

.footer-copyright {
  width: 100%;
  padding: $gridPad;
  text-align: right;
  color: $grey-400;
  font-size: 12px;
}

.btn {
  text-transform: none;
  box-shadow: none;
  border-radius: $btn-border-radius;
  font-size: 15px;
  font-family: $avenir-hv;
  padding: 1rem 4rem;
  letter-spacing: 0.03em;
  cursor: pointer;

  &:hover {
    box-shadow: $btnHoverBoxShadow;
  }

  &-primary {
    background-color: $blue-500;

    &:hover,
    &:active,
    &:focus {
      background-color: $blue-500 !important;
    }
  }

  &-default {
    background-color: $white;
    color: $blue-500 !important;
    box-shadow: $moduleBoxShadow;

    &:hover,
    &:active,
    &:focus {
      background-color: $white !important;
    }
  }

  &-secondary {
    background-color: $grey-50;
    color: $blue-500 !important;

    &:hover,
    &:active,
    &:focus {
      background-color: $grey-50 !important;
    }
  }

  &-small {
    font-size: 12px;
    padding: 12px 16px;
  }

  // any font icons that get placed inside
  i {
    width: 1em;
    font-size: 1em;
    font-weight: bold;
    line-height: 1.2em;
    vertical-align: top;
    margin-left: 0.2em;

    &.pre-label {
      margin: 0 0.2em 0 0;
    }
  }
}

.capitalize {
  text-transform: capitalize;
}

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

.caption {
  color: $grey-600;
  font-size: 12px;
  font-family: $avenir-bk;
  margin-bottom: $gridPad/2;
}

.no-margin {
  margin: 0 !important;
}

// declarative styles - sometimes the simplest way of accounting for design inconsistencies
.font-12 {
  font-size: 12px !important;
}
.font-15 {
  font-size: 15px !important;
}
.font-18 {
  font-size: 18px !important;
}

.m-t-2-pad {
  margin-top: $gridPad*2 !important;
}
.m-t-pad {
  margin-top: $gridPad !important;
}
.m-t-half-pad {
  margin-top: $gridPad/2 !important;
}
.m-t-0 {
  margin-top: 0 !important;
}

.m-r-pad {
  margin-right: $gridPad !important;
}
.m-r-half-pad {
  margin-right: $gridPad/2 !important;
}

.m-b-2-pad {
  margin-bottom: $gridPad*2 !important;
}
.m-b-pad {
  margin-bottom: $gridPad !important;
}
.m-b-half-pad {
  margin-bottom: $gridPad/2 !important;
}
.m-b-0 {
  margin-bottom: 0 !important;
}

.m-l-pad {
  margin-left: $gridPad !important;
}
.m-l-half-pad {
  margin-left: $gridPad/2 !important;
}

/////////////////////////////////////////////////////////
/////////////////////////////////////////////////////////

@include mobile() {
  body {
    // used by mobile nav menu
    transition: transform 300ms $easeOutQuad;

    &.shifted {
      transform: translateX($mobileMenuBodyShift);
    }
  }

  h1 {
    font-size: 30px;
  }

  .nav-bar-container {
    position: relative;
    width: 100%;
    height: auto;
  }

  .body-container {
    width: 100%;
    margin: 0 auto;
    padding-top: 26px
  }

  .btn {
    font-size: 18px;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;

    &:hover {
      box-shadow: none;
    }
  }

  .row--mobile-no-gutters {
    margin-right: -$gridPad;
    margin-left: -$gridPad;

    > .col,
    > [class*="col-"] {
      padding-right: 0;
      padding-left: 0;
    }
  }
}

// shared components
@import 'common/forms';
@import 'common/banner';
@import 'common/perfect_scroll';
