.dashboard__module.calendar {
  .calendar-container {
    display: flex;
    height: 540px;
    border-radius: $moduleBorderRadius;
    box-shadow: $moduleBoxShadow;
    overflow: hidden;
  }

  .hidden-cal-grid {
    display: none;
  }

  .clndr-grid {
    width: 556px;
    background-color: $white;
    font-size: 0px;

    .days {
      .imported-week {
        display: none;
      }

      // class applied on resize by JS. don't allow the mobile slide transforms to affect desktop grid.
      &.no-transform {
        transform: none !important;
      }
    }

    .header-day,
    .day {
      display: inline-flex;
      width: 14%;
      height: 14%;
      text-align: center;
      align-items: center;
      justify-content: center;
    }

    .header-day {
      text-transform: uppercase;
      color: $blue-300;
      font-family: $avenir-hv;
      font-size: 12px;
    }

    .day {
      position: relative;
      cursor: pointer;
      color: $blue-500;
      font-family: $avenir-md;
      font-size: 15px;
      align-items: center;

      .day-square {
        width: 60px;
        height: 60px;
        background-color: rbga(255,0,0,0.5);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &.event:not(.selected) .day-text:after {
        content: '';
        display: block;
        width: 6px;
        height: 6px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 15px);
        background-color: $blue-500;
        border-radius: 50%;
      }

      &.selected .day-square {
        color: $white;
        font-family: $avenir-bl;
        background-color: $blue-500;
        border-radius: 50%;
      }

      &.adjacent-month {
        color: $blue-100;
        &.event:not(.selected) .day-text:after {
          background-color: $blue-100;
        }
      }
    }
  }

  .list__container {
    flex: 1 0 320px;
    display: flex;
    flex-direction: column;
    border-left: 1px solid $black-semi-transp;
    box-shadow: none;
    border-radius: 0;
  }

  .list__header {
    border-radius: 0 3px 0 0;
    flex: 0 0 $li-height;
    font-size: 18px;
    color: $blue-800;
  }

  .dashboard-list {
    overflow: auto;
    margin-bottom: 0;
  }

  .list-item {
    height: 115px;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 26px;

    // no bottom border if it would sit flush with bottom of list
    &:nth-child(n+4):nth-last-child(1) {
      border-bottom: 0;
    }

    .event-type {
      font-family: $avenir-hv;
      color: $blue-200;
      text-transform: uppercase;
    }
  }

  .no-events {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-family: $avenir-md;
    color: $grey-400;
    background-color: $grey-100;
    padding: 0 $gridPad;
    text-align: center;
  }

  // date and month select
  @include mdbSelectPopDown

  .calendar-select {
    display: inline-block;
    margin-bottom: 32px;

    input.select-dropdown {
      font-size: 30px;
      border-width: 2px;
      padding: 12px 0;
      margin: 0;
    }

    li:first-child span {
      font-size: 30px;
      padding: 12px 16px;
    }

    .calendar-month-select {
      margin-right: $gridPad;
    }

    .calendar-month-select .select-dropdown {
      width: 220px;
    }

    .calendar-year-select .select-dropdown {
      width: 138px;
    }
  }

  .calendar-select.is-mobile {
    display: none;
  }
}
