.rich-text {
  &__toolbar {
    border-color: $grey-300 !important;
    background-color: $grey-50;
    padding: $gridPad/2 $gridPad/4 !important;

    // Quill js icons w/in the toolbar
    .ql-stroke {
      stroke: $blue-200 !important;
    }
    .ql-fill {
      fill: $blue-200 !important;
    }
  }

  &__editor {
    border-color: $grey-300 !important;

    &, & > * {
      color: $grey-800;
    }
  }
}
