@import '../variables';

.dev-grid {
  display: none;
}

body.show-dev-grid {
  [class^="col-"] {
    background-color: rgba(20, 20, 20, 0.1);
    border-right: 15px solid $bodyBgColor; // same as page background
    border-left: 15px solid $bodyBgColor; // same as page background
  }

  .dev-grid {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 $grid-gutter-width-base;

    div[class^="col-"] {
      background: none;
      border: 0;

      & > .content {
        width: 100%;
        height: 100%;
        background-color: rgba(255, 0, 0, 0.2);
      }
      height: 100%;
    }
  }
}
