
.dashboard__module.interviews {
  .header-sort {
    // ZD: should be 27%, but the scrollbar on the list makes its total width slightly less.
    // For now assuming no-scroll-bar is an edge case.
    flex: 0 1 27%;

    &.status {
      flex: 0 1 auto;
    }
  }

  .dashboard-list {
    height: auto;
    max-height: 480px;
  }

  .list-item {
    &>div {
      flex: 0 1 27%;
    }

    &__status {
      flex: 0 1 19% !important;
    }

    &:last-child {
      border-bottom: 0px;
    }
  }
}
