@import '../easing';
@import '../variables';
@import '../mixins';
@import '../fonts';
@import '../common/popup_menus';
@import '../common/tables/datatable_theme_acceleration';
@import 'list_popup_menus';

$pageAboveTableHeight: 64px;

.page-lists {
  // don't let popup-menus or re-ordering columns cause scrollbars - overrides bootstrap
  overflow: hidden !important;

  // always let the list fill the window
  .body-container {
    padding: 0;
    max-width: none;
  }

  .footer-copyright {
    display: none; // TODO: does this really go away?
  }

  .list-title {
    font-size: 30px;
    color: $grey-900;
    font-family: $avenir-hv;
    padding-left: $gridPad;
  }

  .acc-table {
    width: 100%;
    height: calc(100vh - #{$pageAboveTableHeight});
  }
}

@import 'mobile';
