$mobileCellMinWidth: 30px;
$mobileTruncatedCellMaxWidth: 80px;
$mobileViewToggleDuration: 800ms;
$mobileMenuHeaderFooterHeight: 64px;
$pagingFooterHeight: 48px;

@include mobile() {
  .page-lists {
    overflow: visible !important;

    .body-container {
      background-color: $white;
      padding-top: 0;
    }

    .list-title {
      font-size: 27px;
      margin: 0;
      padding-top: 26px;
      padding-bottom: 24px;
    }

    .list-controls {
      padding-right: 0;

      &__button {
      }

      &__menu {
        .popup-menu__head {
          height: 64px;
        }
      }
    }

    .acc-table {
      // critical change from desktop that allows page to be scrolled
      // down until the list takes up entire view.
      height: calc(100vh - #{$pagingFooterHeight});
      position: relative;

      &.mobile-view-table {
        .acc-table__mobile-table-scrim {
          opacity: 0;
        }

        .acc-table__mobile-table-toggle-full {
          transition-delay: 0ms;
          transform: translate(-50%, 100%);
          opacity: 0;
        }

        .acc-table__mobile-table-toggle-init {
          transition-delay: 600ms;
          transform: translateY(0%);
          opacity: 1;
        }
      }

      &__mobile-table-scrim {
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        background-color: $scrimColor;
        opacity: 1;
        transition: opacity $mobileViewToggleDuration $easeInOutSine;
      }

      &__mobile-table-toggle-full,
      &__mobile-table-toggle-init {
        position: absolute;
        z-index: 15;
        border-radius: 50%;
        text-align: center;
        background-color: $blue-500;

        i {
          color: $white;
          @include absCenter();
        }
      }

      &__mobile-table-toggle-full {
        $size: 35vw;
        position: fixed;
        top: calc(100vh - 0px);
        left: 50%;
        width: $size;
        height: $size;
        opacity: 1;
        transform: translate(-50%, -130%);
        transition: transform $mobileViewToggleDuration/2 $easeInOutSine 600ms, opacity $mobileViewToggleDuration/2 $easeInOutSine 600ms;

        i {
          font-size: 25vw;
        }
      }

      &__mobile-table-toggle-init {
        bottom: 10px;
        right: 24px;
        width: 34px;
        height: 34px;
        opacity: 0;
        transform: translateY(50%);
        transition: all $mobileViewToggleDuration/2 $easeInOutSine;

        i {
          font-size: 24px;
        }
      }

      &__table {
        thead th,
        tbody td {
          // this plus padding will dictate min width of columns
          min-width: $mobileCellMinWidth;
        }

        td.mobile-max-width-col {
          max-width: $mobileTruncatedCellMaxWidth;
        }

        &-header-bottom {
          // on desktop, icon shown only on hover
          & > [class*="icomoon-"] {
            display: block;
          }
        }
      }

      .pagination-container {
        height: 48px;
        flex-direction: column;
      }

      .dataTables_wrapper {
        .dataTables_info {
          flex: 0 0 48px;
          // swap with .dataTables_paginate
          order: 2;
          background-color: $grey-100;
        }

        .dataTables_paginate {
          background-color: $grey-50;
          border-bottom: 1px solid $black-semi-transp;
        }
      }
    }

    .popup-menu {
      position: fixed;
      z-index: $zindex-mobile-popup-menus;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      &__head {
        display: flex;
        padding-top: 0 !important;
        height: $mobileMenuHeaderFooterHeight !important;
        font-size: 18px;
        background-color: $grey-50;
        justify-content: space-between;
        align-items: center;
        color: $blue-200;
        border-radius: 0;
        border-bottom: none;
        box-shadow: $moduleBoxShadow;
        z-index: 1;

        &:after,
        &:before {
          display: none;
        }

        i {
          color: $blue-500;
        }
      }

      &__body {
        width: auto !important;
        min-width: initial;
        height: calc(100% - #{$mobileMenuHeaderFooterHeight}); // fit header above
        overflow: auto;
        border-radius: 0;
        position: relative;

        &--customize {
          height: calc(100% - #{$mobileMenuHeaderFooterHeight * 2}); // fit header above and confirm buttons below
        }
      }

      &__options-container {
        display: block !important;
      }

      &__option {
        color: $blue-500;
        font-size: 15px;

        &--selected {
          color: $blue-700;
        }
      }

      &__inline-form-group--range {
        flex: 0 1 83px;
      }

      &__results-list > div {
        font-size: 15px;
      }

      &__confirm-buttons {
        position: fixed;
        bottom: 0;
        width: 100%;

        div {
          padding: 16px 32px;
          color: $blue-500;
        }
      }
    }
  }
}
