@import '../easing';
@import '../fonts';
@import '../mixins';
@import '../variables';

$zindex-bar:        20;
$zindex-menu:       10;
$zindex-menu-scrim:  5;

// default state of nav bar is collapsed
// see body.nav-bar-expanded for open/expanded styles
.nav-bar {
  position: absolute;
  z-index: $zindex-bar;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  padding: 0;
  text-align: center;
  background: $blue-500;
  overflow-x: hidden;
  transition: all 300ms $easeOutQuad;

  a:hover, a:focus, a:hover:focus {
    text-decoration: none;
  }

  @include desktop() {
    &__logo,
    &__items {
      visibility: hidden;
      opacity: 0;
    }
  }

  &__logo {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;

    img {
      width: 200px;
      max-width: 100%;
      height: auto;
      margin: 0 auto;
    }
  }

  &__burger {
    position: absolute;
    top: 22px;
    right: 0px;
    width: 20px;
    height: 14px;
    padding: 30px;

    &-inner {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      &,
      &:before,
      &:after {
        position: absolute;
        width: 20px;
        height: 2px;
        background-color: $blue-50;
      }

      &:before,
      &:after {
        content: '';
        left: 0;
      }

      &:before {
        top: -6px;
      }

      &:after {
        top: 6px;
      }
    }
  }

  &__items {
    text-align: left;
    margin: 56px auto 0;
  }

  &__items:last-child {
    margin-top: 36px;
  }

  &__item {
    position: relative;
    margin-left: 18px;

    &--has-new:before {
      @include orangeDot(10px);
      left: -13px;
      top: 10px;
    }

    &.has-caret:after {
      content: '';
      position: absolute;
      right: -($gridPad + 7px);
      top: 3px;
      width: 14px;
      height: 14px;
      background: $white;
      transform: translateX(50%) rotate(45deg);
      opacity: 0;
      transition: all 125ms $easeOutQuad;
    }

    &.has-caret.show-caret:after {
      opacity: 1;
      transform: translateX(0%) rotate(45deg) ;
    }

    &.is-unselected a {
      color: $blue-200;
    }

    // when a flyout is open you may get to this state
    &.is-unselected.is-selected a {
      color: $blue-50;
    }

    a {
      display: block;
      color: $blue-50;
      font-size: 18px;
      line-height: 1.25em;
      padding: 0 0 16px;
      transition: color 150ms $easeOutQuad;
    }
  }

  &__item-count {
    font-family: $avenir-bl;
    padding-left: 0.35em;
  }
}

.nav-bar-menu-scrim {
  position: fixed;
  z-index: $zindex-menu-scrim;
  top: 0; left: 0;
  bottom: 0; right: 0;
  width: 100%;
  height: 100%;
  background-color: $scrimColor;
  opacity: 0;
  visibility: hidden;
  transition: opacity 350ms $easeOutQuad 0ms, visibility 0ms;

  &.active {
    visibility: visible;
    opacity: 1;
  }
}


@mixin expandedNavBarStyles() {
  width: $navBarExpandedWidth;
  padding: $gridPad;

  .nav-bar__logo,
  .nav-bar__items {
    visibility: visible;
    opacity: 1;
  }

  .nav-bar__burger {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    transition: opacity 100ms $easeOutQuad, visibility 0ms linear 200ms;
  }
}

@include desktop() {
  // all inner pages where nav bar default state is collapsed
  body:not(.nav-bar-expanded) {
    .nav-bar__burger {
      cursor: pointer;
      pointer-events: all;
      visibility: visible;
      opacity: 1;
      transition: opacity 200ms $easeOutQuad 200ms;
    }
  }

  // upon clicking the collapsed bar's hamburger, 'is-cxpanded' is added
  .nav-bar.is-expanded {
    .nav-bar__logo,
    .nav-bar__items {
      transition: opacity 200ms $easeOutQuad 300ms;
    }

    @include expandedNavBarStyles();
  }
}

// the min size required for initially expanded nav is 992
// whereas the min size for "desktop" layouts are 768 - hence ...
@include desktopExpandedNav() {
  // dashboard, where nav bar default state is expanded
  body.nav-bar-expanded {
    .nav-bar {
      @include expandedNavBarStyles();
    }
  }
}


