.nav-bar-menu {
  $headerHeight: 78px;
  $border: 1px solid rgba(0, 0, 0, 0.08);

  // TODO: change to fixed when nav-bar changes
  position: absolute;
  z-index: $zindex-menu;
  left: 0;
  top: 0;

  min-width: 370px;
  height: 100%;
  background: $white;

  transform: translateX(-100%); // may need to change
  transition: all $navMenuAnimSpeed $easeOutQuad;

  a:hover, a:focus, a:hover:focus {
    text-decoration: none;
  }

  &.active {
    transform: translateX($navBarExpandedWidth);
    box-shadow: 0 0 7px 0 rgba(0,0,0,0.15), 0 0 7.5px 0 rgba(0,0,0,0.12);
  }

  &--wide {
    min-width: 538px;
  }

  &--multi-section {
    // Jobs & Projects menu a lil diff't than others
    .nav-bar-menu__items {
      height: auto;
    }

    .nav-bar-menu__item {
      display: block;
      padding-top: 16px;
      padding-bottom: 14px;
      font-size: 15px;
      line-height: 1.4em;
      color: $blue-800;

      &:hover {
        // TODO: hover style?
      }
    }
  }

  &__header {
    $headerPad: 26px;
    position: relative;
    background-color: $grey-50;
    padding: $headerPad $gridPad;
    height: $headerHeight;
    border-bottom: $border;
    color: $blue-200;
    font-family: $avenir-hv;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.02em;

    .close-btn {
      display: inline-block;
      position: absolute;
      top: 0;
      right: 0;
      padding: $headerPad $gridPad;

      i {
        color: $blue-500;
        font-size: 1.25em;
        line-height: 24px;

        // two icons, used in different cases, mobile and desktop
        &.icon-back {
          display: block;
        }

        &.icon-x {
          display: none;
        }
      }
    }
  }

  &__action {
    display: block;
    background-color: $blue-50;
    color: $blue-500;
    padding: 29px $gridPad 27px;
    font-size: 15px;
    line-height: 1.4em;
    border-bottom: $border;

    // + icon
    i {
      color: $blue-500;
      vertical-align: top;
      font-size: 1.5em;
      margin-top: -1px;
      margin-left: -4px;
      margin-right: 3px;
    }
  }

  &__items {
    position: relative;
    height: calc(100% - 78px); // $headerHeight
  }

  &__item {
    display: block;
    position: relative;
    padding: 27px $gridPad;
    border-bottom: $border;

    &:hover {
      background-color: $blue-50;
    }

    &--is-new:before {
      @include orangeDot(10px);
      left: 37px;
    }

    .nbmi-content {
      padding: 0 25px;
      font-size: 12px;
      line-height: 1.5em;
      color: $grey-700;

      &--no-new {
        padding-left: 0;
      }
    }

    .nbmi-title {
      font-size: 15px;
      line-height: 1.4em;
      color: $blue-800;
    }

    .del-btn {
      position: absolute;
      top: 50%;
      right: $gridPad;
      transform: translateY(-50%);
      display: inline-block;
      color: $blue-100;
      transition: color 150ms $easeOutQuad;

      &:hover {
        color: $blue-500;
      }
    }
  }

  @import '../common/help_content';

  // a couple discrepancies in the help content styling
  .help-content-guide {
    margin-top: 248px;

    a {
      color: $blue-500;
    }
  }
}

@import 'mobile';
