table.simple-table {
  width: 100%;
  font-size: 12px;

  thead {
    text-transform: uppercase;
    font-family: $avenir-md;
    border-bottom: 1px solid $black-less-transp;

    td {
      color: $grey-800;
    }
  }

  tbody {
    tr {
      border-bottom: 1px solid $black-semi-transp;

      &:last-child {
        border-bottom: 0;
      }
    }
  }

  td {
    padding: $gridPad/2 0;
    color: $grey-600;

    &.emphasized {
      color: $grey-800;
    }

    &.simple-table__checkbox {
      .form-group {
        margin: 0 0 20px;
      }
    }
  }
}
