// clear defaults
input[type="text"],
input[type="password"],
input[type="number"],
input[type="search"],
input[type="email"] {
  -webkit-appearance: none;
  height: auto;
}

@include mdbSelectPopDown;
// override some dropdown styles
.form-select {
  position: relative;
  padding: $gridPad/4 0 $gridPad/2 0;

  .form-select-label {
    color: $blue-400;
    font-size: 12px;
    font-family: $avenir-hv;
    text-transform: uppercase;
    line-height: 10px;
		position: absolute;
    top: 0;
  }

  .select-wrapper {
    input.select-dropdown {
      color: $grey-800;
      font-size: 15px;
      font-family: $avenir-md;
      border-color: $blue-400;
    }

    span.caret {
      border-left: 7px solid $blue-400;
    }
  }
}

.form-control {
  padding-bottom: $gridPad/4;
  padding-top: 22px;
  line-height: normal;
  margin-top: 0;
  margin-bottom: 24px;
}

.form-inline {
  // AK: why was this zero? is there some correct usage that would
  // place margin-having els within .form-inline?
  margin-bottom: 24px; //0;
  flex-flow: row;

  span.inline-title {
    font-size: 15px;
    color: $grey-800;
    font-family: $avenir-md;
    width: 130px;
    margin-right: $gridPad;
  }

  i.inline-icon {
    color: $blue-500;
    margin-left: -$gridPad / 2;
    cursor: pointer;
  }

  .flex-grow,
  .form-group.flex-grow {
    flex: 1 0 auto;
  }

  .flex-shrink,
  .form-group.flex-shrink {
    flex: 0 1 auto;
  }

  .form-group,
  .form-select:not(:last-child) {
    margin-right: $gridPad;
  }

  .form-group:last-child {
    margin-right: 0;
    margin-bottom: 0;
  }

  .form-control {
    width: 100%;
  }

  .form-group {
    // cause inputs to rest on same baseline as
    // CTAs and other inline-block els in the row.
    input[type='text'] {
      // margin-bottom: $gridPad / 4;   AK: actually this breaks some layouts. ugh.
    }
  }
}

.form-inline-separator {
  margin-left: -$gridPad/2;
  margin-right: $gridPad/2;
}

.time-group {
  flex-flow: row;

  .form-select,
  .form-group {
    width: calc((100% - #{$gridPad * 2} ) / 3);
  }
}

.md-form {
  margin-bottom: 24px;

  &.small-font {
    &, & > * {
      font-size: 12px;
    }
  }

  &.date-picker {
    cursor: pointer;

    input {
      cursor: pointer;
    }

    label {
      cursor: pointer;

      &.unfilled {
        transform: none;
      }
    }

    .popup-menu {
      // add 36px on the left for calendar icon inside the menu head
      left: -16px;
      width: calc(100% + 30px);

      &__head {
        padding-left: 52px;

        &:before {
          // date picker popup shouldn't have a caret
          content: '' !important;
        }

        &:after {
          // the calendar icon
          content: $icon-calendar;
          font-family: 'icomoon';
          display: inline-block;
          position: absolute;
          font-size: 22px;
          color: #607d8b;
          left: 16px;
        }
      }
    }

    span.caret {
      position: absolute;
      right: 0;
      top: 33px;
      font-size: 0;
      border-left: 7px solid $blue-400;
      border-bottom: 7px solid transparent;
      transform: translate(0px, -7px) scaleX(0.8) rotate(-135deg);
    }
  }

  label {
    color: $blue-400;
    font-size: 12px;
    font-family: $avenir-hv;
    text-transform: uppercase;
    top: 23px;

    &.active {
      font-size: 12px;
      transform: translateY(-27px);
    }
  }

  i.prefix {
    top: 21px;
    width: auto;
    font-size: 22px;
    color: $blue-500;

    &.active {
      color: $blue-500;
    }

    & ~ input {
      margin-left: $gridPad/2 + 20px;
      width: calc(100% - #{$gridPad/2 + 20})
    }

    & ~ label {
      margin-left: $gridPad/2 + 20px;
    }
  }

  .popup-menu {
    position: absolute;
    top: 8px;
    left: 20px;
    width: calc(100% - #{20 - $gridPad/2});
    z-index: 2;

    &__head {
      padding: 0 $gridPad/2;
      font-family: $avenir-hv;
      color: $blue-400;
      text-transform: uppercase;
      line-height: 49px;
      height: 49px;

      &:before {
        font-family: 'icomoon';
        content: $icon-triangle;
        font-size: 5px;
        position: absolute;
        right: $gridPad/2;
        top: 24px;
        transform: translateY(-50%);
        z-index: 1;
      }
    }
  }
}

input[type="text"],
input[type="number"],
textarea.md-textarea {
  border-color: $blue-400;
  font-size: 15px;
  color: $grey-800;
  font-family: $avenir-md;
  height: auto;

  &:focus:not([readonly]) {
    border-color: $blue-400;
    box-shadow: none;

    & + label {
      font-size: 12px;
      color: $blue-400;
    }
  }
}

// The "USD" in https://cl.ly/303u3E2T2B1f
.secondary-label {
  position: absolute;
  right: 14px;
  top: 21px;
  font-size: 15px;
}

// gulp-sourcemaps chokes if you include this
// in the block above ... *shrug*
input[type="search"] {
  border-color: $blue-400;

  &:focus:not([readonly]) {
    border-color: $blue-400;
    box-shadow: none;
  }
}

.md-form:not(:first-child) textarea {
  margin-top: 8px;
}

.md-form:not(:last-child) textarea {
  margin-bottom: 8px;
}

textarea.md-textarea {
  border: 1px solid $grey-300;
  padding: $gridPad/2;
  box-sizing: border-box;
  height: 160px;
  overflow-y: scroll;

  &::-webkit-input-placeholder {
    color: $blue-400;
    font-size: 12px;
    line-height: 25px;
    font-family: $avenir-hv;
  }

  &:-ms-input-placeholder {
    color: $blue-400;
    font-size: 12px;
    line-height: 25px;
    font-family: $avenir-hv;
  }

  &::-ms-input-placeholder {
    color: $blue-400;
    font-size: 12px;
    line-height: 25px;
    font-family: $avenir-hv;
  }

  & + label {
    left: 17px;

    &.active {
      opacity: 0;
    }
  }
}

.textarea-caption {
  display: inline-block;
  margin-top: 12px;
}

.checkbox-group-label {
  color: $blue-400;
  font-size: 12px;
  font-family: $avenir-hv;
  text-transform: uppercase;
  margin-bottom: 10px;

  &:not(:first-child) {
    margin-top: 8px;
  }
}

.form-group {
  margin-bottom: $gridPad/2;

  &:last-child {
    margin-bottom: $gridPad;
  }

  [type="checkbox"] + label,
  [type="radio"] + label {
    height: auto;
    line-height: 20px;
    box-sizing: border-box;
    font-size: 12px;
    padding-left: 25px;
    font-family: $avenir-md;
    color: $grey-800;
    margin-bottom: 0;
  }

  [type="radio"] {
    & + label {
      padding-left: 28px;
    }

    &.with-gap:not(:checked) + label {
      // ring
      &:before {
        width: 20px;
        height: 20px;
        margin: 0;
        border: 2px solid $blue-400;
      }

      // dot
      &:after {
        width: 20px;
        height: 20px;
        margin: 0;
      }
    }
    &.with-gap:checked + label {
      // ring
      &:before {
        width: 20px;
        height: 20px;
        margin: 0;
        border: 2px solid $blue-700;
      }

      // dot
      &:after {
        width: 20px;
        height: 20px;
        margin: 0;
        border: 2px solid $blue-700;
        background-color: $blue-700;
      }
    }
  }

  [type="checkbox"] {
    &.filled-in:not(:checked) + label {
      // checkmark
      &:before {
        top: 2px;
        left: 2px;
        width: 5px;
        height: 10px;
        border-top-width: 0;
        border-left-width: 0;
        border-radius: 0;
        transform: rotateZ(45deg);
      }

      // box
      &:after {
        top: 1px;
        width: 18px;
        height: 18px;
        border-color: $blue-400;
        border-width: 2px;
        border-radius: 2px;
      }

      &.disabled:after {
        border-color: $grey-400;
      }
    }

    // checked animation end
    &.filled-in:checked + label {
      // checkmark
      &:before {
        top: 2px;
        left: 2px;
        width: 5px;
        height: 10px;
        border-right-color: $white;
        border-bottom-color: $white;
        border-top-width: 0;
        border-left-width: 0;
        border-radius: 0;
        transform: rotateZ(45deg);
      }

      // box
      &:after {
        top: 1px;
        width: 18px;
        height: 18px;
        border-width: 2px;
        border-radius: 2px;
        border-color: $blue-700;
        background-color: $blue-700;
      }
    }

    &.filled-in + label.disabled {
      &:before {
        border-right-color: $white;
        border-bottom-color: $white;
      }

      &:after {
        border-color: $blue-100;
        background-color: $blue-100;
      }
    }
  }
}

// used to slide checkboxes and radio btns under neath form-group inputs
.fit-under-form-group {
  position: relative;
  // so you can click it through the abve inputs' margin.
  z-index: 1;
  margin-top: -$gridPad;
  margin-bottom: $gridPad * 2;
}

.tooltip-icon {
  font-size: 18px;
  vertical-align: top;
  color: $blue-500;
  cursor: pointer;
  margin: 0 3px;
}

@include mobile() {
  .form-group {
    [type="checkbox"] {
      & + label {
        font-size: 15px;
        line-height: 18px;
        padding-left: 32px;
      }
    }
  }
}
