.page-login {
  @include mdbSelectPopDown

  &__lang-select {
    width: 176px;

    // overrides to make text/carets/underlines white
    input.select-dropdown {
      color: $blue-50 !important;
      border-color: $blue-50 !important;
    }

    select {
      color: $blue-50 !important;
      border-bottom: 1px solid $blue-50 !important;
    }

    span.caret {
      border-left: 7px solid $blue-50 !important;
    }

    &:hover {
      input.select-dropdown {
        color: $white !important;
        border-color: $white !important;
      }

      span.caret {
        border-left: 7px solid $white !important;
      }
    }
  }

  &__lang-select.is-desktop {
    float: right;
  }

  &__lang-select.is-mobile {
    display: none;
  }
}
