/**
 * AK: This may need to become *Job Record* tabs if they end
 * up not being useful as a fully abstracted module.
 */

@import '../variables';

$hoverTransition: 150ms;

.tabs {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.tab {
  flex: 0 1 auto;
  position: relative;
  padding: $gridPad/2;
  cursor: pointer;

  // default font style
  font-family: $avenir-hv;
  font-size: 18px;
  color: $blue-200;
  transition: color $hoverTransition $easeOutQuad;

  &.active {
    color: $blue-700;

    &:after {
      content: '';
      position: absolute;
      z-index: 10;
      left: $gridPad/2;
      bottom: 0px;  // -2px? make sure it can be seen
      width: calc(100% - #{$gridPad});
      height: 2px;
      background-color: $blue-700;
      transition: opacity $hoverTransition $easeOutQuad $hoverTransition;
    }
  }

  &:hover {
    color: $blue-500;
  }

  &--deletable {
    transition: all $hoverTransition $easeOutQuad;

    &:hover {
      background-color: $grey-200;
      padding: $gridPad/2 ($gridPad + 22px) $gridPad/2 ($gridPad - 8px);

      i {
        position: absolute;
        transition-duration: $hoverTransition;
        transition-delay: $hoverTransition;
        opacity: 1;
      }
    }

    // hide the active marker on hover
    &.active:hover:after {
      opacity: 0;
      transition-duration: 0ms;
      transition-delay: 0ms;
    }

    i {
      position: absolute;
      top: 10px;
      right: 12px;
      opacity: 0;
      padding: 10px;
      font-size: 16px;
      color: $blue-200;
      transition: opacity 0ms ease-out;

      &:hover {
        color: $blue-500;
      }
    }
  }
}
