.qualification {
  display: flex;
  justify-content: space-between;
  border-radius: 3px;
  padding: $gridPad/2;
  transition: box-shadow 250ms ease-out;
  color: $grey-800;
  font-family: $avenir-lt;

  &:hover {
    box-shadow: $moduleBoxShadow;
  }

  &__copy {
    max-width: 80%;
  }

  &__actions {
    display: flex;
    align-items: center;

    & > * {
      color: $blue-500;
      cursor: pointer;
      padding: 0 4px;
    }
  }

  &__name {
    font-family: $avenir-md;
  }
}
