.evaluation {

  &__header {
    display: flex;
  }

  &__labels {
    justify-content: flex-end;
  }

  &__label {
    flex: 1 0 0;
    text-align: center;
    text-transform: uppercase;
    font-family: $avenir-hv;
  }

  &__rows {
    display: flex;
    flex-direction: column;
  }

  &__row {
    display: flex;
    align-items: center;
    padding: $gridPad/2 0;
    border-bottom: 1px solid $black-semi-transp;

    &:last-child {
      border-bottom: 0;
    }
  }

  &__title,
  &__row-question {
    flex: 1 0 28%;
  }

  &__labels,
  &__row-answers {
    display: flex;
    flex: 1 0 72%;
  }

  &__row-answer {
    flex: 1 0 0;
    text-align: center;

    // to center the radio input
    [type="radio"] + label:before,
    [type="radio"] + label:after {
      left: 5px;
      top: 5px;
    }
  }
}
