@import '../variables';

.help-content {
  &, a {
    color: $grey-700;
    font-size: 12px;
  }

  a {
    color: $blue-500;
    text-decoration: underline;
  }

  h4 {
    color: $blue-800;
    font-family: $avenir-md;
    margin-top: 2em;

    &:first-child {
      margin-top: 0;
    }
  }

  p {
    margin-bottom: 0;
  }

  &-contact {
    padding: 30px 32px;
    border-bottom: 1px solid $black-semi-transp;
  }

  &-guide {
    background-color: $blue-50;

    a {
      display: block;
      padding: $grid-gutter-width-base;
      font-size: 15px;
      font-family: $avenir-hv;
      text-decoration: none;
    }

    i {
      // arrow upper-right
      transform: rotate(-45deg);
      float: right;
    }
  }
}
