.typeahead {

  &.active {
    .typeahead__menu {
      display: block;
    }
  }

  &__menu {
    display: none;
    position: absolute;
    top: -$gridPad;
    left: -$gridPad;
    width: calc(100% + #{$gridPad * 2});
    height: calc(100% + #{$gridPad * 2});

    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    // background: $white;
    box-shadow: $btnHoverBoxShadow;
    border-bottom: 1px solid $black-semi-transp;
  }

  .mdb-autocomplete-wrap {
    box-shadow: none;

    margin-top: 8px;
    left: -$gridPad;
    right: -$gridPad;

    li {
      padding: 9px $gridPad;

      &:hover {
        background: transparent;
      }
    }
  }
}
