//TODO: width will be set per column
$cellMaxWidth: 400px;
$table-padding: 32px;
$table-inner-padding: 24px;
$table-cell-v-padding: 4px;
$rowHeight: 40px;
$tableCellBg: $white;
$tableCellBorder: $grey-300;
$tableFixedColBorder: $black;
$tableHeaderBg: $blue-300;
$tableRowHoverBg: $blue-50;
$evenRowBg: $grey-50;

@import 'table_popup_menus';
@import 'datatable_theme';

body.fs-table-no-scroll {
  overflow: hidden !important;
}

.acc-table {
  position: relative;
  top: 0;
  left: 0;
  background-color: $tableCellBg;

  &.fs-animating {
    transition: all 1s;
    // above perfectScrollbars
    z-index: 10;
  }

  // table empty state
  &.no-records {
    .no-records-msg {
      position: absolute;
      left: 50%;
      transform: translate(-40%, -50%);
      font-size: 15px;
      font-family: $avenir-md;
      color: $grey-400;
    }

    .DTFC_LeftBodyWrapper,
    .dataTables_scrollBody {
      table {
        opacity: 0;
      }
    }
  }

  // for column reorder
  .drop-zone.drop-zone-active {
    background-color: $blue-700;
    color: transparent;
    @include cellInsetShadowLeftRight();

    &.truncate-gradient:after,
    .icomoon-arrow {
      display: none;
    }
  }

  // right-rule anchor columns
  td.right-rule {
    @include cellBorders($right: $tableFixedColBorder);
  }

  // hide flash of unformatted table while JS loads
  table { visibility: hidden; }
  table.dataTable { visibility: visible; }

  .DTFC_ScrollWrapper {
    // override DT JS-resizing
    height: calc(100% - #{$rowHeight}) !important;
    // unless options.forceHeight = false
    &.no-force-height {
      height: auto !important;
    }
  }

  .DTFC_LeftWrapper {
    z-index: 1;
    height: 100% !important;
    overflow: hidden;
    box-shadow: $tableInnerShadow;

    .DTFC_LeftBodyWrapper {
      padding-bottom: 15px;
      box-sizing: content-box;
    }

    // override DT JS-resizing
    // ZD: force fixed col to be large enough to show contents without scrolling
    .DTFC_LeftBodyLiner {
      position: absolute !important;
      height: 100% !important;
      max-height: 100% !important;
    }
  }

  // hide 15px scroll rail on the right
  .DTFC_RightWrapper {
    .DTFC_RightBodyLiner {
      overflow-y: auto !important;
    }

    .DTFC_RightBodyWrapper {
      background-color: $grey-50;
    }
  }

  .dataTables_wrapper {
    position: relative;
    top: 0;
    transition: all 1s;
  }

  .dataTables_scroll {
    overflow: hidden;

    .dataTables_scrollBody {
      // don't allow room for DTCR_pointer to show beyond horz scrollbar
      border-bottom: 0 !important;

      // abs-positioned elements like filter icon shouldn't overflow from these zero-height sizing rows
      th {
        overflow: hidden;
      }
    }

    // texture for draggable column
    .acc-table__table-header-top[data-column-index] {
      @include draggableGrippy(true);
    }
  }

  .DTFC_LeftHeadWrapper,
  .dataTables_scrollHead {
    background-color: $tableHeaderBg;
  }

  // ZD: applies to EVERY <table> created by DT
  &__table {
    thead th {
      white-space: nowrap;
      max-width: $cellMaxWidth;
    }

    tbody {
      tr {
        &:nth-child(2n) {
          background-color: $evenRowBg;
        }

        &.row-hover {
          background-color: $tableRowHoverBg !important;

          td.truncate-gradient:after {
            @include cellTruncateGradient($tableRowHoverBg, $horz-padding);
          }
        }

        &.is-new td:first-child:before {
          @include orangeDot(8);
          transform: translate(0 -50%);
          left: $table-padding / 2;
        }
      }

      a {
        color: $blue-500;
        text-decoration: underline;
      }

      td {
        &.truncate-gradient {
          &:after {
            @include cellTruncateGradient($tableCellBg, $table-padding);
          }

          &.column-hover:after {
            @include cellTruncateGradient($tableCellBg, $table-padding);
          }
        }

        a {
          font-family: $avenir-hv;
        }
      }
    }

    &-header-top {
      height: $table-padding + $table-cell-v-padding;
      line-height: $table-padding;
      padding-right: $table-padding * 1.5 !important;
      text-transform: uppercase;
      position: relative;
      font-family: $avenir-hv;

      &:not(.sorting_disabled) {
        cursor: pointer;
      }

      &.sorting_asc,
      &.sorting_desc {
        .icomoon-arrow {
          color: $orange;
          visibility: visible;
          vertical-align: text-bottom;
        }
      }

      &.sorting_desc {
        .icomoon-arrow {
          transform: rotate(180deg);
        }
      }

      .icomoon-arrow {
        position: relative;
        display: inline-block;
        margin-left: 10px;
        font-size: 16px;
        transition: transform 0.2s $easeOutQuad;
        visibility: hidden;
      }
    }

    &-col-filter,
    &-header-bottom {
      font-family: $avenir-hv;
      color: $blue-500;
    }

    &-header-bottom {
      position: relative;
      height: $table-padding + 4;
      padding: 0 $table-padding $table-cell-v-padding $table-padding;

			// hover styles where there's a filter-type applied
      &[data-filter-type] {
        // additional padding so dropdown arrow isn't crowded
        padding-right: $table-padding * 1.5 !important;
        cursor: pointer;

				&:before {
	        font-family: 'icomoon';
	        content: "\e906";
	        font-size: 24px;
	        position: absolute;
	        right: $table-padding / 2;
	        top: 50%;
	        transform: translateY(-50%);
	        z-index: 1;
	      }
      }

      &.column-hover {
        background-color: $grey-50;
      }

      &.truncate-gradient[data-filter-type] {
        &:after {
          @include cellTruncateGradient($tableHeaderBg, $table-padding * 2, 36);
        }
      }

      // TODO: Is there a better place for these filter-specific styles?
      // Shared by popup-menu__head
      // star rating
      .star {
        display: inline;
        color: $blue-100;
        font-size: 14px;
        padding: 0 1px;

        &.filled {
          color: $blue-500;
        }
      }

      // flag icon
      i {
        font-size: 18px;
        line-height: 32px;
      }
    }

    &-col-filter {
      position: absolute;
			// above PerfectScrollbars
      z-index: 11;
      top: $table-inner-padding + $table-cell-v-padding;
    }
  }

  &__foot-controls {
    display: flex;
    align-items: center;
    padding-right: $table-padding;

    &-btn {
      height: 100%;
      cursor: pointer;
      margin-left: $table-inner-padding;
      display: flex;
      align-items: center;
      position: relative;
    }
  }

  // .list-fullscreen-btn {
  //   position: relative;
  //   width: $table-padding;
  //   height: $table-padding;
  //   border-radius: 50%;
  //   background-color: $blue-800;
  //   cursor: pointer;
  //
  //   i {
  //     color: $white;
  //     @include absCenter();
  //   }
  // }

  .pagination-container {
    height: $rowHeight;
    display: flex;
    font-family: $avenir-md;
    background-color: $blue-300;
    color: $white;
    position: relative;
    // above the table and its shadows
    z-index: 1;

    .custom-paging {
      position: relative;
      flex-grow: 1;
    }
  }

  // special table cells
  td,
  th {
    // requires .td-btn-group as cell class to work correctly
    &.td-btn-group {
      padding: $table-cell-v-padding $table-cell-v-padding * 2;
    }

    .td-btn-group {
      &__wrapper {
        height: $table-padding;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        box-sizing: border-box;
        overflow: hidden;
      }

      &__btn {
        flex: 1 0 auto;
        height: 100%;
        padding: 0 24px;
        color: $white;
        font-family: $avenir-hv;
        background-color: $blue-500;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3px;
        position: relative;

        &:hover {
          background-color: $blue-600;
        }

        & ~ .td-btn-group__btn {
          margin-left: 3px;
        }

        &--orange {
          background-color: $orange;

          &:hover {
            background-color: $orange;
          }
        }

        &--in-progress {
          border-radius: $gridPad / 2;

          &:before {
            font-size: 24px;
            font-family: 'icomoon';
            content: $icon-in-progress;
            margin-right: 8px;
          }
        }
      }
    }

    .td-percent__rail {
      width: 133px;
      height: 12px;
      background-color: $blue-200;
      display: inline-block;
      margin-right: 8px;
      vertical-align: text-top;
      border: 2px solid $blue-200;
      border-radius: 3px;
    }

    .td-percent__bar {
      height: 100%;
      background-color: $blue-700;
      border-radius: 1px;
    }

    .star {
      display: inline;
      color: $grey-200;
      font-size: 14px;
      padding: 0 1px;

      &.filled {
        color: $grey-500;
      }
    }

    .heart {
      font-size: 24px;
      cursor: pointer;
      display: inline-block;

      &:after {
        font-family: 'icomoon' !important;
        content: $icon-heart-filled;
        color: $blue-100;
      }

      &.active:after {
        color: $blue-700
      }
    }

    .form-group {
      margin-bottom: 0;

      [type="checkbox"].filled-in:not(:checked) + label {
        margin-bottom: 0;
        font-family: $avenir-hv;
        color: $blue-100;

        &:after {
          border-color: $blue-100;
        }
      }
    }
  }

  // extend the column grid beyond actual cells
  // .fake-cell-container {
  //   overflow: visible;
  // }

  .fake-rows-container {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }

  .fake-cols-container {
  }

  .fake-cell {
    pointer-events: none;

    &.fake-row,
    &.fake-row-col {
      width: 100%;
      height: $rowHeight;
      position: absolute;

      &:nth-child(2n) {
        background-color: $evenRowBg;
      }

      &.row-hover {
        background-color: $tableRowHoverBg !important;
      }
    }
  }

  // alternating row stripes for fake-column extensions
  // color order depends on whether last real row is even or odd
  .fake-cols-container,
  .fake-row-col {
    background-image: repeating-linear-gradient($white, $white $rowHeight, $grey-50 $rowHeight, $grey-50 $rowHeight * 2) !important;

    &.even {
      background-image: repeating-linear-gradient($grey-50, $grey-50 $rowHeight, $white $rowHeight, $white $rowHeight * 2) !important;
    }
  }
}

// temporary col reorder elements
.DTCR_pointer {
  display: none;
}

.DTCR_clonedTable {
  overflow: visible !important;
  // above perfectScrollbars and pointer line
  z-index: 11;
  box-shadow: $popup-box-shadow;

  th {
    background-color: $blue-300;
    color: $white;
  }

  th,
  td {
    padding-left: 16px !important;
    box-shadow: none !important;
  }

  .fake-cols-container {
    background-image: repeating-linear-gradient($white, $white $rowHeight, $grey-50 $rowHeight, $grey-50 $rowHeight * 2) !important;

    &.even {
      background-image: repeating-linear-gradient($grey-50, $grey-50 $rowHeight, $white $rowHeight, $white $rowHeight * 2) !important;
    }
  }

  .acc-table__table-header-top {
    @include draggableGrippy(true);
    &:before {
      right: 18px;
    }
  }
}


////////////////////////////////////
// OVERRIDES FOR DT DEFAULT THEME //
////////////////////////////////////

table.dataTable {
  color: $grey-800;
  font-size: 12px;

  thead,
  tfoot {
    th {
      font-weight: normal;
    }
  }

  thead th,
  thead td {
    padding: $table-cell-v-padding $table-padding / 2 0 ;
    color: $white;
    @include cellBorders($right: $tableCellBorder)

    // larger left padding for first cells in row
    &:first-child { // added selector
      padding: $table-cell-v-padding $table-padding / 2 0 $table-padding;
    }

    &:active,
    &:focus { // added selector
        outline: none;
    }
  }

  tbody {
    tr {
      position: relative;

      &.selected {
        background-color: $table-row-selected;
      }
    }

    th,
    td {
      padding: 0 $table-padding / 2;
      @include cellBorders($right: $tableCellBorder)

      // larger left padding for first cells in row
      &:first-child { // added selector
        padding: 0 $table-padding / 2 0 $table-padding;
      }
    }
  }

  thead {
    .sorting,
    .sorting_asc,
    .sorting_desc {
      background-image: none;
    }
  }

  td {
    white-space: nowrap;
    max-width: $cellMaxWidth;
    height: $rowHeight;
    overflow: hidden;
    position: relative;
  }
}

.dataTables_wrapper {
  font-size: 12px;

  // Filtering input
  .dataTables_filter {
    input {
      margin-left: 0px;
      box-sizing: border-box;
    }
  }

  // Table info
  .dataTables_info {
    color: $white;
    white-space: nowrap;
    padding: 0 $table-padding;
    display: flex;
    align-items: center;
  }

  // Paging
  .dataTables_paginate {
    height: 100%;
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    // override DT defaults
    float: none;
    text-align: middle;
    padding-top: 0;

    .paginate_button {
      // override DT defaults
      box-sizing: inherit;
      display: inline-block;
      min-width: auto;
      padding: 0;
      margin-left: 0;
      text-align: inherit;
      text-decoration: none !important;
      cursor: pointer;
      color: $white !important;
      border: none !important;
      background: none !important;
      border-radius: 0;
      margin-right: $table-inner-padding;
      outline: none !important;
      box-shadow: none !important;
      line-height: $rowHeight;

      &:active {
        font-family: $avenir-bl;
      }

      &.current,
      &.current:hover {
        color: $white !important;
        font-family: $avenir-bl;
      }

      &.previous,
      &.next {
        position: relative;
        text-transform: uppercase;
        font-family: $avenir-hv;
        margin: 0 50px;
      }

      &.previous:before,
      &.previous:after,
      &.next:before,
      &.next:after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 2px solid $blue-500;
        border-left: 2px solid $blue-500;
        transform: rotate(135deg);
      }

      &.next:before {
        position: absolute;
        top: 50%;
        right: 5px;
        transform: translateY(-50%) rotate(135deg);
      }

      &.previous:before,
      &.previous:after {
        transform: rotate(-45deg);
      }

      &.previous:after {
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%) rotate(-45deg);
      }

      &.disabled,
      &.disabled:hover,
      &.disabled:active {
        &:before,
        &:after {
          border-color: $grey-400;
        }

        cursor: default;
        color: $grey-400 !important;
      }
    }
  }
}
