/**
 * Our custom style for AddToCalendar
 */

@import 'add_to_calendar_base';

$horz-padding: 16px;
$vert-padding: 8px;

.atc-style-menu-wb {

  .atcb-list {
    display: none;
    visibility: visible;
    position: absolute;
    border: 0;
    padding: $vert-padding 0;
    border-radius: $popup-border-radius 0 $popup-border-radius $popup-border-radius;
    // TODO: smooth fade-in not implemented yet.
    // need to add a new class addition to trigger that
    // since we're using display: none/block now instead of visibility
    opacity: 0;
    transition: opacity 150ms $easeOutQuad;

    &.show {
      display: block;
      opacity: 1;
    }

    // hide overlapping shadow of list
    &:after {
      content: '';
      width: 64px;
      position: absolute;
      z-index: 1;
      top: -4px;
      right: 0;
      height: 3px;
      background-color: $white;
    }
  }

  .atcb-link {
    color: $blue-500 !important;
    text-decoration: none !important;

    margin: 0 21px;

    &:focus~ul,
    &:active~ul {
      opacity: 1;
    }

    &.active {
      right: 0;
      height: 58px;
      width: 64px;
      color: $blue-100;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: $popup-box-shadow;
      border-top-left-radius: $moduleBorderRadius;
      border-top-right-radius: $moduleBorderRadius;
      z-index: 2;
      margin: 0;
      margin-top: -17px;
      margin-bottom: -17px;
    }
  }

  .atcb-item {
    line-height: normal;
    &:hover {
      background-color: transparent;
    }
  }

  .atcb-item-link,
  .atcb-item-link:active,
  .atcb-item-link:focus,
  .atcb-item-link:hover {
    font-size: 12px;
    line-height: 32px;
    font-family: $avenir-md;
    color: $blue-300;
    padding: 0 $horz-padding;
    &:hover {
      background-color: transparent;
      color: $blue-500;
    }
  }
}
