.simple-row-header {
  position: relative;

  &__link {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.simple-row-container {
  margin-bottom: $gridPad;
}

.simple-row {
  display: flex;
  border-bottom: 1px solid $black-semi-transp;
  padding: 15px 0;

  &--wrap {
    flex-wrap: wrap;

    .simple-row__value {
      margin-top: $gridPad/2;
    }
  }

  &:last-child {
    border-bottom: 0;
  }

  &__label {
    min-width: 55%;
    width: 55%;
    color: $grey-800;
    padding-right: $gridPad;

    &--full-width {
      width: 100%;
      padding-right: 0;
    }

    &--auto-width {
      width: auto;
      min-width: auto;
      padding-right: $gridPad/4;
    }
  }

  &__value {
    color: $grey-600;
  }
}
