@font-face {
  font-family: "icomoon";
  src: url("../font/icomoon/icomoon.eot?#iefix") format("embedded-opentype"), url("../font/icomoon/icomoon.woff2") format("woff2"), url("../font/icomoon/icomoon.woff") format("woff"), url("../font/icomoon/icomoon.ttf") format("truetype"), url("../font/icomoon/icomoon.svg") format("svg");
}

[class^="icomoon-"], [class*=" icomoon-"] {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 24px;
}

