@import '../common/panels';
@import '../common/comments';

.page-job-record {
  font-size: 12px;
  color: $grey-700;

  // this may need to go in index.scss... TBD
  h1 {
    font-size: 24px;
    color: $blue-200;
  }

  &__tab-content > .row {
    margin-bottom: 60px;
  }

  &__ctas {
    .btn {
      width: 100%;
      font-size: 12px;
      margin: 0 0 8px;
      padding: 19px 0;
    }
  }

  &__comments {
    // TODO: may want to make more general to all
    // table modules on this page
    margin-top: 10px;

    h1 {
      padding-top: 10px;
    }

    .panel {
      // will scroll via js after this
      max-height: 236px;
      margin-top: 10px;
    }

    .comment {
      padding: 20px $gridPad/2;
      border-bottom: 1px solid $black-semi-transp;
    }

    .comment-time {
      display: block;
    }

    .comment-message {
      // TODO: figure out ellipses
      // white-space: nowrap;
      height: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  // the left column for all panels
  // okay? a lil hokey
  .panel-row > .row > *:first-child {
    font-weight: 700;
    color: $grey-800;
  }
}
