@import '../variables';
@import '../easing';
@import '../fonts';
@import '../mixins';

$pageAboveTableHeight: 64px;

.cardsRow {
  height: calc(100vh - #{$pageAboveTableHeight});
  width: 100%;
  overflow: auto;
  background-color: lightgrey;
  white-space: nowrap;
  padding: 27px 30px;
  box-sizing: border-box;
  font-size: 0;
}

.infoCard {
  width: calc(33.33% - 12px);
  height: 100%;
  display: inline-flex;
  flex-direction: column;
  flex: 1 0 auto;
  font-size: 12px;
  vertical-align: top;
  background-color: white;

  &:not(:last-child) {
    margin-right: 18px;
  }
}

.infoCard__header,
.infoCard__footer {
  width: 100%;
  height: 63px;
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  padding: 0 9px;
}

.infoCard__footer {
  justify-content: space-between;
}

.infoCard__body {
  overflow: auto;
  padding: 18px;
  flex: 1 1 auto;
  font-size: 33px;
}

.infoCard__avatar {
  width: 45px;
  height: 45px;
  background-color: gray;
  border-radius: 50%;
  flex: 0 0 auto;
  margin-right: 15px;
}

.infoCard__headerInfo {
  flex: 1 1 auto;
}

.infoCard__checkbox {
  width: 16px;
  height: 16px;
  border: 1px solid gray;
}

.infoCard__footerBtn {
  height: 27px;
  width: 45px;
  border-radius: 9px;
  background-color: gray;
}
