.page-login {

  &__login-form {
    input:focus ~ .page-login__login-form-forgot {
      opacity: 0;
    }
  }

  &__login-form-forgot {
    position: absolute;
    right: 0;
    top: 1rem;
    font-size: 0.8rem;
    font-family: $avenir-md;
    color: $blue-200;
    transition: all 250ms $easeOutQuad;
    text-decoration: underline;
  }
}

@include mobile() {
  .page-login {
    &__welcome {
      margin-bottom: 30px;
    }

    &__welcome-body {
      margin-top: 20px;
    }

    &__login-form {
      button {
        width: 100%;
        //background-color: #FDFEFE; // reuseable color?
      }
    }
  }
}
