$vert-padding: 16px;
$horz-padding: 32px;
$line-height: 32px;

.popup-menu {

  // list control popups
  $btnWidth: 198px;
  $btnHeight: 53px;

  &--list-controls {
    position: absolute;
    bottom: 100%;
    right: 0px;
    // z-index: 15;

    .popup-menu {
      &__body {
        position: relative;
        border-radius: 3px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;

        &--customize {
          width: 500px;
        }

        &--export {
          width: 200px;
        }
      }

      &__options-container {
        display: flex;
        padding: 24px 0 0;
      }

      &__options-column {
        flex: 1;
        padding: 0 24px;

        .form-group {
          height: 24px;
          margin-bottom: 16px;
        }

        .form-group input[type=checkbox]+label {
          font-family: $avenir-md;
          white-space: nowrap;
        }
      }

      &__confirm-buttons {
        border-radius: 0;
      }
    }
  }
}
