$vert-padding: 16px;
$horz-padding: 24px;
$line-height: 24px;

.popup-menu {
  &__head {

    // TODO: Is there a better place for these filter-specific styles?
    // Shared by acc-table__header-bottom
    .star {
      display: inline;
      color: $blue-100;
      font-size: 14px;
      padding: 0 1px;

      &.filled {
        color: $blue-700;
      }
    }

    i {
      font-size: 18px;
      line-height: 51px;
    }
  }

  &__body {
    &--range,
    &--options {
      width: auto;
      min-width: 100%;
    }

    &--slider {
      min-width: 330px;
    }

    &--search,
    &--search-available {
      min-width: 330px;
      overflow: hidden;
    }

    &--cost-range,
    &--date-range,
    &--date {
      min-width: 375px;
    }

    &--stars {
      min-width: 204px;
    }
  }

  &__inline-form {
    border-bottom: 1px solid $black-semi-transp;
  }

  &__inline-form-group {
    position: relative;

    &--range {
      flex: 0 1 83px;
      input {
        width: 100%;
      }
    }

    &--date-range {
      font-size: 0px;
      border-bottom: 1px solid $blue-500;
      box-shadow: none;
      transition: all 0.3s;
      padding-top: $line-height;
      flex: 0 1 135px;

      &.active {
        box-shadow: 0 1px 0 0 $blue-500;
      }

      &.invalid {
        border-bottom-color: $orange;
        box-shadow: 0 1px 0 0 $orange;

        input {
          color: $orange;

          &:focus {
            border-color: $orange;
            box-shadow: none !important;
          }
        }
      }

      label {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
      }

      input {
        display: inline-block;
        box-shadow: none !important;
        border-bottom: none !important;
        text-align: center;

        &.month-input {
          width: 22%;
          text-align: left;
        }

        &.day-input {
          width: 35%;
        }

        &.year-input {
          width: 43%;
        }
      }

      .date-slash {
        font-size: 15px;
        color: $grey-300;
        width: 0px;
        display: inline-block;
        transform: translateX(-2px);
      }
    }
  }

  &__search {
    padding: $horz-padding;
    border-bottom: 1px solid $black-semi-transp;
    display: flex;
    align-items: center;

    i.icomoon-search {
      font-size: 16px;
      width: 32px;
    }
  }

  &__search-bar {
    width: 100%;
    border-bottom: 2px solid $blue-500;
    display: flex;
    align-items: center;
    justify-content: center;

    input {
      flex: 1 0 auto;
      outline: none;
      border: none;
      font-size: 15px;
      line-height: 32px;
      font-family: $avenir-md;
      color: $blue-500;
    }

    i.clear-x {
      font-size: 15px;
      line-height: 18px;
      width: 18px;
      height: 18px;
      background-color: $blue-100;
      border-radius: 50%;
      color: $white;
      text-align: center;
      cursor: pointer;
    }
  }

  &__results {
    position: relative;
    //padding: $vert-padding 0;
    max-height: 248px;
  }

  &__results-list {
    & > div {
      min-height: $line-height;
      // display: flex;
      // align-items: center;
      cursor: pointer;
      color: $blue-500;
      font-family: $avenir-bk;
      padding: 7px $horz-padding 7px 56px;

      &:hover {
        background-color: $grey-50;
      }

      &:first-child {
        margin-top: 6px;
      }

      &:last-child {
        margin-bottom: 6px;
      }
    }

    // .pad-multilines {
    //   padding: 6px 0;
    // }

    .hilite {
      color: $orange;
      font-family: $avenir-hv;
    }
  }

  &__slider {
    padding: $vert-padding $table-padding $table-padding $table-padding;

    span {
      display: inline-block;
      line-height: $line-height;
      margin-bottom: $vert-padding;
      font-size: 15px;
      font-family: $avenir-md;
    }

    @include rangeSlider;
  }

  &__stars {
    padding: $table-padding;
    display: flex;
    justify-content: space-between;

    .star {
      color: $blue-100;
      font-size: 28px;
      cursor: pointer;

      &.filled {
        color: $blue-700;
      }
    }
  }

  &__clndr-grid {
    font-size: 0px;
    padding: 16px 0;

    .clndr-controls {
      display: flex;
      height: 32px;
      font-size: 18px;
      font-family: $avenir-hv;
      padding: 0 $table-padding;
      align-items: center;
      color: $blue-500;

      .month {
        flex: 1 0 auto;
        text-align: center;
      }

			.clndr-next-button,
			.clndr-previous-button {
				cursor: pointer;

				&:after {
					content: '';
	        width: 9px;
	        height: 9px;
	        border-top: 2px solid $blue-500;
	        border-left: 2px solid $blue-500;
	        display: block;
	        transform: rotate(-45deg);
				}
			}

      .clndr-next-button:after {
        transform: rotate(135deg);
      }
    }

    .header-day,
    .day {
      display: inline-flex;
      width: 13.5%;
      height: 100%;
      text-align: center;
      align-items: center;
      justify-content: center;
      font-size: 12px;
    }

    .days-of-the-week,
    .days {
      text-align: center;
    }

    .days-of-the-week {
      height: 32px;
      margin: 8px 0 6px 0;
    }

    .header-day {
      text-transform: uppercase;
      color: $grey-400;
      font-family: $avenir-hv;
    }

    .day {
      position: relative;
      cursor: pointer;
      color: $blue-500;
      font-family: $avenir-md;
      align-items: center;
      height: 50px;

      &.adjacent-month {
        visibility: hidden;
      }

      &.is-between .day-square {
        background-color: $blue-50;
      }

      &.selected,
      &.is-from,
      &.is-to {
        .day-square {
          color: $white;
          font-family: $avenir-bl;
          background-color: $blue-500;
        }
      }

      .day-square {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    &.show-from .day.is-from .day-square,
    &.show-to .day.is-to .day-square {
      color: $white;
      font-family: $avenir-bl;
      background-color: $blue-500;
    }
  }
}
