.dashboard__module.notifications {
  // first module after Todos, which have smaller margin bottoms
  // than the rest of the modules ... better way to spec this?
  margin-top: 60px;

  .header-sort {
    // ZD: should be 59%, but the scrollbar on the list makes its total width slightly less.
    // For now assuming no-scroll-bar is an edge case.
    &.name-desc {
      flex: 0 1 59%;
    }
  }

  .dashboard-list {
    height: auto;
    max-height: 499px;
  }

  .list-item {
    height: 100px;
    transition: transform 400ms $easeInOutCubic, height 300ms $easeOutQuad;

    &:last-child {
      border-bottom: 0px;
    }

    &.pre-delete {
      transform: translateX(-100%);
    }

    &.post-delete {
      height: 1px;
    }

    &--is-new:before {
      // aligns orange dot with title
      // instead of b/w title and desc
      top: 42%;
    }

    &__name-desc {
      flex: 0 0 59%;
    }
  }
}
