$railSize: 12px;
$railColor: $grey-100;
$barSize: 6px;
$barColor: #CED8DD;

.ps {
  // this specificity is req'd to override PS's default stylesheet.
  & > .ps__rail-x,
  & > .ps__rail-y {
    z-index: 10;
    opacity: 1 !important;
    background: none !important;
    transition: height .2s linear,
                width .2s ease-in-out,
                border-radius .2s ease-in-out;

    & > .ps__thumb-x,
    & > .ps__thumb-y {
      border-radius: $barSize / 2;
      background-color: $barColor;
      // overrides a PS style that gives safari issues.
      transition: background-color .2s linear,
                  height .2s linear,
                  width .2s ease-in-out,
                  border-radius .2s ease-in-out
    }

    & > .ps__thumb-x {
      bottom: 2px;
    }

    & > .ps__thumb-y {
      right: 0;
    }
  }

  &:not(.ps-in-scrolling) > .ps__rail-x {
    height: $barSize;
  }

  &:not(.ps-in-scrolling) > .ps__rail-y {
    width: $barSize;
  }

  &.ps--in-scrolling {
    &.ps--x > .ps__rail-x {
      height: $railSize;
    }

    &.ps--y > .ps__rail-y {
      width: $railSize;
    }
  }

  & > .ps__rail-x {
    height: $railSize;
    // border-top: 1px solid rgba(0, 0, 0, 0.08);
    // border-bottom: 1px solid rgba(0, 0, 0, 0.08);

    // ZD: bars were not keeping up with force-resized parent
    width: 100% !important;
  }

  & > .ps__rail-y {
    width: $railSize;
    // border-left: 1px solid rgba(0, 0, 0, 0.08);
    // border-bottom: 1px solid rgba(0, 0, 0, 0.08);

    // ZD: bars were not keeping up with force-resized parent
    height: 100% !important;
  }

  &,
  &:hover {
    & > .ps__rail-x:hover,
    & > .ps__rail-y:hover {
      & > .ps__thumb-x,
      & > .ps__thumb-y {
        border-radius: $railSize / 2;
      }
    }

    & > .ps__rail-y:hover {
      width: $railSize;
      & > .ps__thumb-y {
        width: $railSize;
      }
    }

    & > .ps__rail-x:hover {
      height: $railSize;
      & > .ps__thumb-x {
        height: $railSize;
      }
    }

  }
}
