@import '../easing';
@import '../grid';
@import '../variables';

$smallGridGutters: (
  xs: $gridPad/2,
  sm: $gridPad/2,
  md: $gridPad/2,
  lg: $gridPad/2,
  xl: $gridPad/2
);
$todoHeight: 64px;

// TODO: we could get more specific w this... ?
.dashboard__module--todos {
  margin-bottom: 60px;
}

.todos {
  @include make-row($smallGridGutters);
  margin-top: -$gridPad/4;
}

.todo {
  @include make-col-ready($smallGridGutters);
  @include make-col(12);
  // @include mobile() {
  //   @include make-col(12);
  // }

  height: $todoHeight;
  margin: 0;

  &:first-child .todo__content {
    border-radius: $moduleBorderRadius $moduleBorderRadius 0 0;
  }

  &:last-child .todo__content {
    border-radius: 0 0 $moduleBorderRadius $moduleBorderRadius;
    border-bottom-width: 1px;
  }

  &--empty {
    .todo__content {
      background-color: $grey-50;
      padding: 22px 0;
      color: $grey-400;
      border-radius: $moduleBorderRadius !important;
      box-shadow: $moduleBoxShadow;
      justify-content: center;

      &:hover {
        background-color: $grey-50;
      }
    }
  }

  &--is-new:before {
    @include orangeDot(16px);
    top: 50%;
    left: $gridPad/4;
  }

  &__content {
    display: flex;
    flex: 0 0 1;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: $white;
    transition: background-color 200ms $easeOutQuad;
    box-shadow: $moduleBoxShadow;

    &:hover {
      background-color: $blue-50;
    }

    &:hover, &:focus {
      text-decoration: none;
    }
  }

  &__count {
    flex: 0 0 65px;
    height: 100%;
    text-align: center;
    font-family: $avenir-bl;
    font-size: 24px;
    line-height: $todoHeight;
    color: $blue-500;
    border-right: 1px solid rgba(0, 0, 0, 0.08);
  }

  &__title {
    padding: 0 18px;
    font-size: 15px;
    color: $blue-800;
  }
}
