@import '../variables';
@import '../easing';
@import '../fonts';
@import '../mixins';
@import '../common/popup_menus';
@import '../common/add_to_calendar';

$li-height: 75px;
$li-padding: 32px;

.page-dashboard {
  h1 {
    color: $blue-200;
    font-family: $avenir-hv;
    font-size: 30px;
    line-height: 1.2em;
    margin-bottom: 22px;
  }

  .dashboard__module {
    margin-bottom: 120px;

    .list__container {
      border-radius: $moduleBorderRadius;
      box-shadow: $moduleBoxShadow;

      &--empty {
        .header-sort {
          cursor: default;
          color: $grey-400;
          pointer-events: none;

          &:after {
            display: none !important;
          }
        }

        .list-item--empty {
          display: flex;
        }
      }
    }

    .list__header {
      font-size: 12px;
      font-family: $avenir-hv;
      color: $blue-300;
      text-transform: uppercase;
      line-height: $li-height;
      background-color: #fff;
      padding: 0 $li-padding;
      border-bottom: 1px solid $black-semi-transp;
      border-radius: 3px 3px 0 0;
      display: flex;
      align-items: center;
    }

    .header-sort:hover,
    .header-sort[data-sort-dir] {
      &:after {
        content: '';
        width: 9px;
        height: 9px;
        border-top: 2px solid $blue-300;
        border-left: 2px solid $blue-300;
        margin-left: 15px;
        transition: transform 0.2s $easeOutQuad;
      }
    }

    .header-sort[data-sort-dir]:after {
      display: none;
    }

    .header-sort[data-sort-dir='up']:after {
      display: inline-block;
      transform: translateY(2px) rotate(45deg);
    }
    .header-sort[data-sort-dir='down']:after {
      display: inline-block;
      transform: translateY(-3px) rotate(225deg);
    }

    .header-sort {
      cursor: pointer;
      &:hover {
        color: $blue-500;
        &:after {
          border-top: 2px solid $blue-500;
          border-left: 2px solid $blue-500;
        }
      }
    }

    .header-sort:hover:after {
      display: inline-block;
    }

    .header-sort:not([data-sort-dir='up']):hover:after {
      transform: translateY(-3px) rotate(225deg);
    }

    .dashboard-list {
      position: relative;
      left: -10px;
      width: calc(100% + 10px);
      margin-bottom: 0;
    }

    .list-item {
      position: relative;
      width: calc(100% - 10px);
      left: 10px;
      background-color: #fafafa;
      padding: 0 $li-padding;
      height: $li-height;
      display: flex;
      align-items: center;
      border-bottom: 1px solid $black-semi-transp;
      font-size: 12px;
      font-family: $avenir-bk;
      color: $grey-700;
      transition: background-color 200ms $easeOutQuad;

      &:hover {
        background-color: $blue-50;
      }

      &:last-of-type {
        border-bottom: 0;
      }

      &--is-new:before {
        @include orangeDot(16px);
      }

      &--empty {
        display: none;  // unless list-container has list-container--empty
        height: 94px;
        justify-content: center;
        font-size: 15px;
        color: $grey-400;

        &:hover {
          background-color: $grey-50;
        }
      }

      .title {
        font-size: 15px;
        font-family: $avenir-md;
        color: $blue-800;
      }

      .end-icon {
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-50%);
        right: $gridPad;
        cursor: pointer;
        color: $blue-100;

        &:hover,
        &:focus {
          color: $blue-500;
          a {
            text-decoration: none;
          }
        }

        &.cal-btn {
          right: 0;
          height: 100%;
          width: 84px;
          display: flex;
          align-items: center;
          justify-content: center;
          //padding: 26px $gridPad;

          // when showing the calendar menu,
          // allows menu to appear over next list item's icon.
          &.active {
            background: $white;
            z-index: 2;
            color: $blue-500;
            border-radius: 3px 3px 0 0;
          }
        }
      }
    }

    // special look for these two empty modules
    &.documents,
    &.utilities {
      .list-item--empty {
        height: 100%;
        background-color: transparent;
      }
    }
  }

  @import 'todos';
  @import 'calendar';
  @import 'notifications';
  @import 'interviews';
}

// not scoped because this gets cloned and appended
// to body when the interview calendar menu is opened.
.atc-style-menu-wb.clone {
  transform: translate(-50%, -50%);

  .cal-btn {
    position: absolute;
    right: 0;
    height: 57px;
    width: 64px;
    color: $blue-100;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: $popup-box-shadow;
    border-top-left-radius: $moduleBorderRadius;
    border-top-right-radius: $moduleBorderRadius;

    // when showing the calendar menu,
    // allows menu to appear over next list item's icon.
    &.active {
      position: relative;
      background: $white;
      z-index: 2;
      color: $blue-500;
      border-radius: 3px 3px 0 0;
    }
  }

  .atcb-list {
    left: auto;
    right: 0;
    width: 168px;
  }
}

@import 'mobile';
