@import '../easing';
@import '../variables';
@import '../mixins';
@import '../fonts';
@import '../common/popup_menus';
@import '../common/tabs';
@import '../common/tables/datatable_theme_acceleration';
@import '../common/rich-text';
@import '../common/category-table';

// unsure where to put this
// height of quill rich text editor in Job Record modals
.modal .rich-text__editor {
  max-height: 340px;
}

// TODO:
// if you want to break out styles from each major tab
// ... Hire tab was dev'd first so most of the stuff below
// pertains to that and can be pulled out at some point ...
@import 'details';

// used in Edit modals
@import 'qualification';

$pageAboveTableHeight: 104px;
$horz-padding: 24px;

.page-job-record {
  // don't let popup-menus or re-ordering columns cause scrollbars - overrides bootstrap
  overflow-y: auto !important;
  overflow-x: hidden !important;

	&:not(.page-job-record--details) .body-container {
    padding: 0;
		max-width: none;
	}

  .footer-copyright {
    display: none;
  }

  &__table-dropdown {
    position: relative;
    margin-bottom: $gridPad;

    ul.dropdown-content {
      overflow: visible !important;

      li {
        position: relative;
      }
    }

    &--new-candidates:not(.open):before {
      @include orangeDot(14px);
      // keep above dropdown menu
      z-index: 19;
    }

    &--new-candidates.open {
      // assumes candidates is always 1st option
      li:nth-child(2):before {
        @include orangeDot(14px);
      }
    }
  }

  @include mdbSelectPopDown

  &__table-select {
    width: 242px;
    margin-left: $gridPad / 2;

    .select-wrapper input.select-dropdown {
      font-size: 15px;
      border-width: 2px;
    }

    li:first-child span {
      font-size: 15px;
    }

    &.is-mobile {
      display: none;
    }
  }

  .acc-table {
    width: 100%;
    height: calc(100vh - #{$pageAboveTableHeight});

    // special styles for the compare candidates table
    &[data-table='compare'] {
      .DTFC_LeftBodyWrapper {
        padding: 0;
      }

      // back to candidates button
      th:first-child {
        padding-left: 16px;

        .td-btn-group__btn {
          height: $gridPad !important;
          padding: 0;
          text-transform: none;
        }
      }

      // special table cells
      td {
        // adjust special cell styles for col-1 title cells
        &.align-right:not(:first-child) {
          text-align: right;
        }
        &.align-top:first-child {
          padding-top: 17px;
        }

        ul {
          margin: 17px 0;
          white-space: normal;
          line-height: 18px;
          width: 300px - ($gridPad * 2);

          li {
            position: relative;
            padding-left: 10px;

            &:before {
              content: '';
              width: 3px;
              height: 3px;
              border-radius: 50%;
              position: absolute;
              display: block;
              background-color: $grey-800;
              top: 8px;
              left: 0 ;
            }
          }
        }

        .td-percent__wrapper {
          display: flex;
          width: 100%;
        }

        .td-percent__rail {
          flex: 1 0 auto;
        }

      }

      // fixed column acting as header with sorting functionality
      th:first-child,
      td:first-child {
        cursor: pointer;
        font-family: $avenir-hv;
        color: $blue-500;

        .icomoon-arrow {
          position: relative;
          display: inline-block;
          margin-left: 10px;
          font-size: 12px;
          transition: transform 0.2s $easeOutQuad;
          visibility: hidden;

          &.asc,
          &.desc {
            color: $orange;
            visibility: visible;
          }

          &.asc {
            transform: rotate(0deg);

            &.flip-anim {
              animation: .2s $easeOutQuad;
              @include animation-keyframes {
                from { transform: rotate(180deg); }
                to { transform: rotate(0deg); }
              }
            }
          }

          &.desc {
            transform: rotate(180deg);

            &.flip-anim {
              animation: .2s $easeOutQuad;
              @include animation-keyframes {
                from { transform: rotate(0deg); }
                to { transform: rotate(180deg); }
              }
            }
          }
        }
      }

      th {
        height: 56px;

        &:first-child {
          color: $white;
        }

        // Adjust grippy position for taller th
        &:before,
        &:after {
          top: 13px !important;
        }
      }

      .acc-table__table-header-top:not(:first-child) {
        text-transform: none;
        text-decoration: underline;
        color: $white;
      }

      .icomoon-x-close {
        position: absolute;
        right: $gridPad;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }
    }

    .DTFC_RightWrapper {
      z-index: 1;
      height: 100% !important;

      .DTFC_RightBodyLiner {
        height: 100% !important;
        max-height: 100% !important;
      }
    }

    &__next-section-btn {
      color: $blue-500;
      font-size: 9px;
      cursor: pointer;
      display: inline-block;
      padding-left: 15px;
      margin-left: auto;
    }

    &__foot-controls-btn {
      border-radius: 3px;
      background-color: $blue-700;
      padding: 0 $table-padding / 2;
      height: calc(100% - 8px);
      font-family: $avenir-hv;
    }
  }

  // Change the height of the DTColReorder pointer
  .DTCR_clonedTable {
    .acc-table__table-header-top {
      text-transform: none;
      text-decoration: underline;
      color: $blue-700;

      .clear-x {
        font-size: 14px;
        position: absolute;
        right: $gridPad;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }
    }
  }

  .DTCR_pointer {
    width: 0px;

    &:after {
      content: '';
      postion: absolute;
      display: block;
      top: 0;
      left: 0;
      height: 100%; // necessary?
      width: 3px;
      background-color: inherit;
      box-shadow: inherit;
    }
  }
}
